import { Box, Text } from "grommet";
import React from "react";
import { Link } from "react-router-dom";

/*The code is a React component named "AppBreadcrumbs" that 
uses Grommet and React Router. It renders a visual navigation trail based 
on the provided "hierarchy" prop, with each breadcrumb as a 
clickable link separated by a forward slash ("/").
 */
const AppBreadcrumbs = ({ hierarchy }) => {
  return (
    <Box direction="row" align="center">
      {hierarchy.map((item, index) => (
        <Box key={index} direction="row" align="center">
          <Link to={item.link}>{item.label}</Link>
          {index < hierarchy.length - 1 && <Text margin="xsmall">{"/"}</Text>}
        </Box>
      ))}
    </Box>
  );
};

export default AppBreadcrumbs;
