import { Box, Text } from "grommet";
import React from "react";
import { Close } from "grommet-icons";

/*
renders an error message display area.
 It takes in an array of errors, a getErrorMessage function, an onClearError function, and children as props. 
 It maps through the errors array to render each error message with a close button. 
 The component wraps the error messages and the children components in a Box container.
 */
const ErrorProvider = ({ errors, getErrorMessage, onClearError, children }) => {
  return (
    <Box>
      <Box
        align="center"
        margin={errors.length ? "medium" : "none"}
        gap="small"
      >
        {errors.map((error, index) => {
          return (
            <Box
              key={index}
              direction="row"
              gap="medium"
              pad="small"
              round="small"
              background="warningTileBackgroundColor"
            >
              <Text key={index}>{getErrorMessage(error)}</Text>
              <Box onClick={() => onClearError(error)}>
                <Close />
              </Box>
            </Box>
          );
        })}
      </Box>
      {children}
    </Box>
  );
};

export default ErrorProvider;
