import React, { useState, useEffect } from "react";
import {
  Box,
  Button,
  TextInput,
  Heading,
  Text,
  FileInput,
  Select,
  DataTable,
  Meter, // Import the Meter component from Grommet
} from "grommet";
import { Download, Trash } from "grommet-icons";
import { useTranslation } from "react-i18next";

const ModelManagement = () => {
  const { t } = useTranslation();
  const [modelId, setModelId] = useState("");
  const [modelFileName, setModelFileName] = useState("");
  const [repoId, setRepoId] = useState("");
  const [models, setModels] = useState([]);
  const [isLoading, setIsLoading] = useState(false);
  const [statusMessage, setStatusMessage] = useState("");
  const [selectedFile, setSelectedFile] = useState(null);
  const [selectedOption, setSelectedOption] = useState("");
  const [selectedMode, setSelectedMode] = useState("");
  const [progress, setProgress] = useState(0); // State to track progress

  useEffect(() => {
    fetchModels();
  }, []);

  const fetchModels = () => {
    setIsLoading(true);
    fetch(`${process.env.REACT_APP_SERVER_URL}/list_models`)
      .then((response) => response.json())
      .then((data) => {
        setModels(data);
        setIsLoading(false);
      })
      .catch((error) => {
        console.error("Error fetching models:", error);
        setStatusMessage(t("errorFetchingModels"));
        setIsLoading(false);
      });
  };

  const handleDownload = (isGeneralRepo = false) => {
    if (!isGeneralRepo && (!modelId.trim() || !modelFileName.trim())) {
      alert(t("provideBothModelIdAndFileName"));
      return;
    }

    setIsLoading(true);
    setStatusMessage(t("modelIsDownloading"));
    setProgress(0); // Reset progress

    let url = `${process.env.REACT_APP_SERVER_URL}/download_model`;
    let body = {
      model_id: modelId,
      model_file_name: modelFileName,
    };

    if (isGeneralRepo) {
      url = `${process.env.REACT_APP_SERVER_URL}/download_general_model`;
      body = {
        repo_id: repoId,
      };

      // Add model_type based on selectedOption
      if (selectedOption === "Download HF Model") {
        body.model_type = "hf";
      } else if (selectedOption === "Download ONNX Model") {
        body.model_type = "onnx";
      } else {
        alert(t("invalidModelType"));
        setIsLoading(false);
        return;
      }
    }

    // Simulating progress update (use actual progress value in a real scenario)
    const progressInterval = setInterval(() => {
      setProgress((prevProgress) => {
        if (prevProgress < 100) {
          return prevProgress + 10;
        } else {
          clearInterval(progressInterval);
          return prevProgress;
        }
      });
    }, 500);

    fetch(url, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(body),
    })
      .then((response) => response.json())
      .then((data) => {
        setStatusMessage(data.message);
        setIsLoading(false);
        setProgress(100); // Set progress to 100% when done
        fetchModels();
      })
      .catch((error) => {
        console.error("Error downloading model:", error);
        setStatusMessage(t("errorDownloadingModel"));
        setIsLoading(false);
        clearInterval(progressInterval);
      });
  };

  const handleFileChange = (e) => {
    setSelectedFile(e.target.files[0]);
  };

  const handleUpload = () => {
    if (!selectedFile) {
      alert(t("pleaseSelectFileToUpload"));
      return;
    }

    // Check if the selected file already exists in the models list
    const fileAlreadyExists = models.some(
      (model) =>
        model.name.trim().toLowerCase() ===
        selectedFile.name
          .replace(/\.[^/.]+$/, "")
          .trim()
          .toLowerCase()
    );
    if (fileAlreadyExists) {
      alert(t("fileAlreadyExistsInSystem"));
      return; // Stop the upload process if the file exists
    }

    const formData = new FormData();
    formData.append("file", selectedFile);

    setIsLoading(true);
    setStatusMessage(t("uploadingFile"));
    setProgress(0); // Reset progress

    // Simulating progress update (use actual progress value in a real scenario)
    const progressInterval = setInterval(() => {
      setProgress((prevProgress) => {
        if (prevProgress < 100) {
          return prevProgress + 10;
        } else {
          clearInterval(progressInterval);
          return prevProgress;
        }
      });
    }, 500);

    fetch(`${process.env.REACT_APP_SERVER_URL}/upload_model`, {
      method: "POST",
      body: formData,
    })
      .then((response) => response.json())
      .then((data) => {
        setStatusMessage(data.message);
        setIsLoading(false);
        setProgress(100); // Set progress to 100% when done
        fetchModels();
        // Reset selection fields after successful upload
        setSelectedFile(null);
        setSelectedMode("");
        setSelectedOption("");
      })
      .catch((error) => {
        console.error("Error uploading file:", error);
        setStatusMessage(t("errorUploadingFile"));
        setIsLoading(false);
        clearInterval(progressInterval);
      });
  };

  const handleDelete = (modelName) => {
    if (window.confirm(t("confirmDeleteModel"))) {
      fetch(`${process.env.REACT_APP_SERVER_URL}/delete_model`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ model_name: modelName }),
      })
        .then((response) => response.json())
        .then((data) => {
          setStatusMessage(data.message);
          fetchModels();
        })
        .catch((error) => {
          console.error("Error deleting model:", error);
          setStatusMessage(t("errorDeletingModel"));
        });
    }
  };
  // Reset progress and status message when a new model type is selected
  const handleOptionChange = (value) => {
    setSelectedOption(value);
    setProgress(0); // Reset progress
    setStatusMessage(""); // Reset status message
    setModelId(""); // Clear modelId if switching options
    setModelFileName(""); // Clear modelFileName if switching options
    setRepoId(""); // Clear repoId if switching options
    setSelectedFile(null); // Clear file selection when switching modes
  };

  const handleModeChange = (value) => {
    setSelectedMode(value);
    setProgress(0); // Reset progress
    setStatusMessage(""); // Reset status message
    setModelId(""); // Clear modelId if switching mode
    setModelFileName(""); // Clear modelFileName if switching mode
    setSelectedFile(null); // Clear file selection when switching modes
  };

  return (
    <Box pad="large">
      <Heading level={2} textAlign="start">
        {t("genAIModelManagement")}
      </Heading>

      <Box margin={{ bottom: "medium" }} flex={{ shrink: 0 }} overflow="auto">
        <Select
          options={[
            { label: t("ggufDownloadUpload"), value: "Download GGUF Model" },
            { label: t("hfDownload"), value: "Download HF Model" },
            { label: t("onnxDownload"), value: "Download ONNX Model" },
          ]}
          labelKey="label" // Use labelKey to display the label
          valueKey={{ key: "value", reduce: true }} // Use valueKey to manage value
          value={selectedOption}
          onChange={({ value }) => handleOptionChange(value)}
          disabled={isLoading}
          placeholder={t("selectOption")}
        />
      </Box>

      {selectedOption === "Download GGUF Model" && (
        <Box
          margin={{ bottom: "medium" }}
          gap="medium"
          flex={{ shrink: 0 }}
          overflow="auto"
        >
          <Heading level={3} margin="none">
            {t("ggufModelManagement")}
          </Heading>
          <Box
            direction="row"
            margin={{ bottom: "medium" }}
            gap="medium"
            flex={{ shrink: 0 }}
          >
            <Select
              options={[
                { label: t("download"), value: "Download" },
                { label: t("upload"), value: "Upload" },
              ]}
              labelKey="label" // Use labelKey to display the label
              valueKey={{ key: "value", reduce: true }} // Use valueKey to manage value
              onChange={({ value }) => handleModeChange(value)} // Handle value change
              disabled={isLoading}
              placeholder={t("selectOption")}
              value={selectedMode}
            />
          </Box>

          {selectedMode === "Download" && (
            <Box
              direction="row"
              margin={{ bottom: "medium" }}
              gap="medium"
              flex={{ shrink: 0 }}
            >
              <Box flex={{ shrink: 0 }} direction="row">
                <Text margin={{ bottom: "xsmall" }}>{t("modelId")}</Text>
                <TextInput
                  id="model-id"
                  placeholder={t("modelIdPlaceholder")}
                  value={modelId}
                  onChange={(e) => setModelId(e.target.value)}
                  disabled={isLoading}
                />
              </Box>
              <Box flex={{ shrink: 0 }} direction="row">
                <Text margin={{ bottom: "xsmall" }}>{t("modelFileName")}</Text>
                <TextInput
                  id="model-file-name"
                  placeholder={t("modelFileNamePlaceholder")}
                  value={modelFileName}
                  onChange={(e) => setModelFileName(e.target.value)}
                  disabled={isLoading}
                />
              </Box>
            </Box>
          )}

          {selectedMode === "Upload" && (
            <Box
              margin={{ bottom: "medium" }}
              gap="medium"
              flex={{ shrink: 0 }}
            >
              <FileInput
                id="file-upload"
                onChange={handleFileChange}
                accept=".gguf"
                disabled={isLoading}
              />
            </Box>
          )}
          <Box
            direction="row"
            margin={{ bottom: "medium" }}
            gap="medium"
            flex={{ shrink: 0 }}
          >
            <Button
              label={
                isLoading
                  ? t("processing")
                  : selectedMode === "Download"
                  ? t("downloadGGUFModel")
                  : t("uploadGGUFModel")
              }
              onClick={
                selectedMode === "Download"
                  ? () => handleDownload(false)
                  : handleUpload
              }
              primary
              disabled={isLoading}
            />
          </Box>
        </Box>
      )}

      {selectedOption === "Download HF Model" && (
        <Box margin={{ bottom: "medium" }} gap="medium" flex={{ shrink: 0 }}>
          <Heading level={3} margin="none">
            {t("downloadHFModel")}
          </Heading>
          <Box>
            <Text margin={{ bottom: "xsmall" }}>{t("repositoryId")}</Text>
            <TextInput
              id="repo-id"
              placeholder={t("repositoryIdPlaceholder")}
              value={repoId}
              onChange={(e) => setRepoId(e.target.value)}
              disabled={isLoading}
            />
          </Box>
          <Box direction="row">
            <Button
              label={isLoading ? t("downloading") : t("downloadHFModelButton")}
              onClick={() => handleDownload(true)}
              primary
              disabled={isLoading}
            />
          </Box>
        </Box>
      )}

      {selectedOption === "Download ONNX Model" && (
        <Box margin={{ bottom: "medium" }} gap="medium" flex={{ shrink: 0 }}>
          <Heading level={3} margin="none">
            {t("downloadONNXModel")}
          </Heading>
          <Box>
            <Text margin={{ bottom: "xsmall" }}>{t("repositoryId")}</Text>
            <TextInput
              id="repo-id"
              placeholder={t("repositoryIdPlaceholder")}
              value={repoId}
              onChange={(e) => setRepoId(e.target.value)}
              disabled={isLoading}
            />
          </Box>
          <Box direction="row">
            <Button
              label={
                isLoading ? t("downloading") : t("downloadONNXModelButton")
              }
              onClick={() => handleDownload(true)}
              primary
              disabled={isLoading}
            />
          </Box>
        </Box>
      )}

      {/* Display Meter (progress indicator) */}
      <Box
        margin={{ top: "medium" }}
        width="large"
        flex={{ shrink: 0 }}
        overflow="auto"
      >
        <Meter
          values={[{ value: progress }]}
          max={100}
          size="xlarge"
          thickness="medium"
        />
      </Box>
      {/* Status Message */}
      <Box margin={{ top: "medium" }} flex={{ shrink: 0 }} overflow="auto">
        <Text>{statusMessage}</Text>
      </Box>

      {/* Downloaded Models Table */}
      <Box margin={{ top: "large" }} flex={{ shrink: 0 }} overflow="auto">
        <Heading level={3} margin={{ bottom: "small" }}>
          {t("downloadedModels")}
        </Heading>
        <DataTable
          columns={[
            {
              property: "name",
              header: t("modelName"),
            },
            {
              property: "type",
              header: t("type"),
            },
            {
              property: "actions",
              header: t("actions"),
              render: (datum) => (
                <Box direction="row" gap="small">
                  <Button
                    icon={<Download />}
                    onClick={() => window.open(datum.downloadUrl, "_blank")}
                    disabled={isLoading}
                  />
                  <Button
                    icon={<Trash />}
                    onClick={() => handleDelete(datum.name)}
                    disabled={isLoading}
                  />
                </Box>
              ),
            },
          ]}
          data={models}
        />
      </Box>
    </Box>
  );
};

export default ModelManagement;
