import React, { useEffect, useState } from "react";
import { Box, Select, Button, Text } from "grommet";
import { useNavigate } from "react-router-dom"; // Import useNavigate
import { useTranslation } from "react-i18next";

const ModelSelectionChats = () => {
  const { t: i18n } = useTranslation();
  const [models, setModels] = useState([]);
  const [selectedModel, setSelectedModel] = useState(null);
  const [loading, setLoading] = useState(false); // State for loading status
  const [error, setError] = useState(null); // State for error handling
  const navigate = useNavigate(); // Initialize useNavigate

  // Fetch the list of available models from the backend
  useEffect(() => {
    fetch(`${process.env.REACT_APP_SERVER_URL}/list_trained_models`)
      .then((response) => response.json())
      .then((data) => {
        setModels(data);
      })
      .catch((err) => {
        console.error("Error fetching models:", err);
      });
  }, []);

  // Handle the request to load the selected model
  const handleLoadModel = () => {
    if (selectedModel) {
      setLoading(true); // Show loading indicator
      setError(null); // Reset error state

      // Send request to load the model
      fetch(`${process.env.REACT_APP_SERVER_URL}/load_model/`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ model_name: selectedModel }),
      })
        .then((response) => {
          if (!response.ok) {
            throw new Error("Failed to load model");
          }
          return response.json();
        })
        .then(() => {
          // Navigate to chat page after model is successfully loaded
          navigate(`/chats?model=${selectedModel}`);
        })
        .catch((err) => {
          console.error("Error loading model:", err);
          setError(i18n("error_loading_model")); // Set error message
        })
        .finally(() => {
          setLoading(false); // Stop loading indicator
        });
    }
  };

  return (
    <Box fill align="center" justify="center">
      {/* Model selection dropdown */}
      <Select
        options={models}
        value={selectedModel}
        onChange={({ option }) => setSelectedModel(option)}
        placeholder={i18n("select_model")}
        disabled={loading} // Disable select while loading
        margin={{ vertical: "small" }}
      />

      {/* Proceed button or loading/error state */}
      {selectedModel && !loading && (
        <Button
          margin={{ vertical: "small" }}
          label={i18n("proceed_to_chat")}
          onClick={handleLoadModel} // Load model before proceeding to chat
          primary
        />
      )}

      {/* Loading indicator */}
      {loading && <Text>{i18n("loading_model_message")}</Text>}

      {/* Error message */}
      {error && <Text color="status-critical">{error}</Text>}

      {/* Instructional message if no model is selected */}
      {!selectedModel && !loading && (
        <Text margin={{ top: "medium" }}>{i18n("select_model_message")}</Text>
      )}
    </Box>
  );
};

export default ModelSelectionChats;
