import { Box, Button, DropButton, Header } from "grommet";
import React from "react";
import { useNavigate } from "react-router-dom";
import i18n from "../translations/i18n";
import { useTranslation } from "react-i18next";
import { getAuth, signOut } from "firebase/auth";
import { HeaderButton } from "./ComponentsLib";
import { Language, Logout, User, UserSettings } from "grommet-icons";

/*
renders a header with language selection and user account options, 
including a dropdown menu for changing the language 
and a dropdown menu for user-related actions like logging out.
*/
const AppHeader = () => {
  const auth = getAuth();
  const user = auth.currentUser;

  const navigate = useNavigate();
  const { t } = useTranslation();

  const logOut = async () => {
    await signOut(auth);
    navigate("/login", { replace: true });
  };

  const onChangeLanguage = (language) => {
    window.localStorage.setItem("appLanguage", language);
    i18n.changeLanguage(language);
  };

  return (
    <Box flex background="brand" justify="center" align="end">
      <Header>
        <DropButton
          plain
          hoverIndicator
          background="brand"
          align="center"
          key={"changeLanguage"}
          alignSelf="start"
          label={t("language")}
          icon={<Language size="small" pad="true" />}
          dropAlign={{ top: "bottom", right: "right" }}
          dropContent={
            <Box pad="medium">
              <HeaderButton
                hoverIndicator
                plain
                alignSelf="start"
                primary={i18n.language === "en"}
                onClick={() => onChangeLanguage("en")}
                label="EN"
                border={{
                  color: i18n.language === "en" ? "white" : "primary-2",
                  size: "small",
                }}
              />
              <HeaderButton
                plain
                hoverIndicator
                alignSelf="start"
                primary={i18n.language === "ja"}
                onClick={() => onChangeLanguage("ja")}
                label="JA"
                border={{
                  color: i18n.language === "ja" ? "white" : "primary-2",
                  size: "small",
                }}
              />
              <HeaderButton
                plain
                hoverIndicator
                alignSelf="start"
                primary={i18n.language === "cn"}
                onClick={() => onChangeLanguage("cn")}
                label="CN"
                border={{
                  color: i18n.language === "cn" ? "white" : "primary-2",
                  size: "small",
                }}
              />
            </Box>
          }
        />
        <DropButton
          plain
          hoverIndicator
          label={user.displayName || t("account")}
          icon={<User size="small" />}
          dropAlign={{ top: "bottom", right: "right" }}
          dropContent={
            <Box pad="medium">
              <Button
                hoverIndicator
                plain
                label={t("settings")}
                icon={<UserSettings size="small" />}
                onClick={() => navigate("/settingsPage")} // Navigate to the settings page
                border={{ color: "primary-2", size: "small" }}
              />
              <Button
                hoverIndicator
                plain
                label={t("logout")}
                onClick={logOut}
                icon={<Logout size="small" />}
                border={{ color: "primary-2", size: "small" }}
              />
            </Box>
          }
        />
      </Header>
    </Box>
  );
};

export default AppHeader;
