import React, { useState } from "react";
import {
  Box,
  Form,
  FormField,
  TextInput,
  Button,
  Text,
  Heading,
  Image,
} from "grommet";
import { Link, useNavigate } from "react-router-dom";
import {
  validateEmail,
  validatePassword,
  validateConfirmedPassword,
  validateFirstName,
  validateLastName,
} from "../utils/validations";
import ErrorProvider from "./ErrorProvider";
import { Google } from "grommet-icons";
import {
  createUserWithEmailAndPassword,
  getAuth,
  updateProfile,
  GoogleAuthProvider,
  signInWithPopup,
  setPersistence,
  browserSessionPersistence,
} from "firebase/auth";
import { createDisplayName } from "../utils/utils";

const Signup = () => {
  const auth = getAuth();

  const [value, setValue] = useState({
    firstName: "",
    lastName: "",
    email: "",
    password: "",
    confirmPassword: "",
    displayName: "",
    uid: "",
  });

  const signInWithGoogle = () => {
    const auth = getAuth();
    const provider = new GoogleAuthProvider();
    setPersistence(auth, browserSessionPersistence)
      .then(() => {
        signInWithPopup(auth, provider)
          .then(async (result) => {
            const user = result.user;

            // Extract necessary data from the user object
            const { displayName, email, uid } = user;

            // Assign the uid value to the value object
            setValue((prevValue) => ({
              ...prevValue,
              uid: uid,
            }));

            // Store signup data in the local database
            await handleSave({ value: { displayName, email, uid } });

            navigate("/", { replace: true });
          })
          .catch((error) => {
            // Handle Errors here.
            const errorMessage = error.message;
            addError(errorMessage);
          });
      })
      .catch((error) => {
        // Handle Errors here.
        const errorMessage = error.message;
        addError(errorMessage);
      });
  };

  const [errors, setErrors] = useState([]);
  // eslint-disable-next-line no-unused-vars
  const addError = (error) => setErrors(errors.concat([error]));
  const onClearError = (errorToClear) => {
    const newErrors = errors.filter((error) => error !== errorToClear);
    setErrors(newErrors);
  };

  const navigate = useNavigate();

  const onSignup = async ({ value }) => {
    const { email, password, firstName, lastName } = value;

    createUserWithEmailAndPassword(auth, email, password)
      .then(async (userCredential) => {
        // Signed in
        const user = userCredential.user;
        user.displayName = createDisplayName(firstName, lastName);

        updateProfile(user, {
          displayName: createDisplayName(firstName, lastName),
        });

        // Extract necessary data from the user object
        const { displayName, uid } = user;

        // Assign the uid value to the value object
        setValue((prevValue) => ({
          ...prevValue,
          uid: uid,
        }));
        await handleSave({ value: { displayName, email, uid, password } });
        navigate("/", { replace: true });
      })
      .catch((error) => {
        const errorMessage = error.message;
        addError(errorMessage);
      });
  };

  const handleSave = async ({ value }) => {
    const requestOptions = {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(value),
    };

    fetch(
      `${process.env.REACT_APP_SERVER_URL}/setNewUserAccount`,
      requestOptions
    )
      .then((response) => response.json())
      .then((data) => {
        console.log(data.message);
        navigate("/", { replace: true });
      })
      .catch((error) => {
        console.error("Error saving signup data:", error);
        addError("Failed to save signup data. Please try again.");
      });
  };

  // eslint-disable-next-line no-unused-vars
  const ColoredLine = ({ color }) => (
    <hr
      style={{
        color: color,
        backgroundColor: color,
        height: 5,
      }}
    />
  );

  return (
    <ErrorProvider
      errors={errors}
      getErrorMessage={(error) => error}
      onClearError={onClearError}
    >
      <Box align="center">
        <Image
          src={require("../assets/CLF-logo_main_black.png")}
          height={40}
          margin={{ vertical: "medium" }}
        />
        <Box margin={{ vertical: "medium" }} align="center" elevation="medium">
          <Box
            background="white"
            pad="small"
            round="small"
            margin="medium"
            align="center"
          >
            <Heading level={4} margin="none">
              {"Sign Up"}
            </Heading>
            <Box direction="row" gap="medium" justify="center" margin="medium">
              <Button
                label="Sign up using Google"
                color="white"
                type="button"
                primary
                icon={<Google color="plain" />}
                size="large"
                onClick={signInWithGoogle}
                style={{
                  boxShadow: "black 0px 0px 0px 2px",
                }}
              />
            </Box>
            <Text>{"or"}</Text>
            <Box pad="small" round="small" margin="medium" width="xmedium">
              <Form
                value={value}
                onChange={(nextValue) => setValue(nextValue)}
                validate="submit"
                onSubmit={(userData) => {
                  onSignup(userData);
                }}
              >
                <Box direction="row" gap="medium">
                  <FormField
                    flex
                    htmlFor="firstName"
                    name="firstName"
                    label={"First Name"}
                    validate={validateFirstName}
                  >
                    <TextInput id="firstName" name="firstName" />
                  </FormField>
                  <FormField
                    flex
                    htmlFor="lastName"
                    name="lastName"
                    label={"Last Name"}
                    validate={validateLastName}
                  >
                    <TextInput id="lastName" name="lastName" />
                  </FormField>
                </Box>
                <FormField
                  htmlFor="email"
                  name="email"
                  label={"Email"}
                  validate={validateEmail.bind(null, "email")}
                >
                  <TextInput id="email" name="email" />
                </FormField>
                <FormField
                  htmlFor="password"
                  name="password"
                  label={"Password"}
                  validate={validatePassword}
                >
                  <TextInput type="password" id="password" name="password" />
                </FormField>
                <FormField
                  htmlFor="confirmPassword"
                  name="confirmPassword"
                  label={"Confirm Password"}
                  validate={validateConfirmedPassword}
                >
                  <TextInput
                    type="password"
                    id="confirmPassword"
                    name="confirmPassword"
                  />
                </FormField>
                <Box
                  direction="row"
                  gap="medium"
                  justify="center"
                  margin="medium"
                >
                  <Button
                    type="submit"
                    color="#C28835"
                    primary
                    style={{ color: "white" }}
                    label={"Sign Up"}
                  />
                </Box>
              </Form>
              <Box direction="row" gap="small" justify="center">
                <Text>{"Already signed up?"}</Text>
                <Link to="/login">{"Login"}</Link>
              </Box>
            </Box>
          </Box>
        </Box>
      </Box>
    </ErrorProvider>
  );
};

export default Signup;
