import React, { useState } from "react";
import {
  Box,
  Form,
  FormField,
  TextInput,
  Button,
  Text,
  Heading,
  Image,
} from "grommet";
import { Link, useLocation, useNavigate } from "react-router-dom";
import { validateEmail, validatePassword } from "../utils/validations";
import ErrorProvider from "./ErrorProvider";
import { Google } from "grommet-icons";
import {
  getAuth,
  signInWithPopup,
  GoogleAuthProvider,
  signInWithEmailAndPassword,
  sendPasswordResetEmail,
  setPersistence,
  browserSessionPersistence,
} from "firebase/auth";
import { useTranslation } from "react-i18next";

const provider = new GoogleAuthProvider();
provider.addScope("https://www.googleapis.com/auth/contacts.readonly");

const Login = () => {
  const [value, setValue] = useState({ email: "", password: "" });

  const location = useLocation();
  const navigate = useNavigate();

  const auth = getAuth();
  const [forgotPasswordClicked, setForgotPasswordClicked] = useState(false);

  const onForgotPassword = async ({ value }) => {
    const { email } = value;

    sendPasswordResetEmail(auth, email)
      .then(() => {
        addError(i18n("passwordResetEmailSent"));
      })
      .catch((error) => {
        const errorMessage = error.message;
        addError(errorMessage);
      });
  };

  const signInWithGoogle = () => {
    setPersistence(auth, browserSessionPersistence)
      .then(() => {
        signInWithPopup(auth, provider)
          .then(async (result) => {
            const user = result.user;
            const { displayName, email, uid } = user;

            setValue((prevValue) => ({
              ...prevValue,
              uid: uid,
            }));

            await handleSave({ value: { displayName, email, uid } });

            navigate("/", { replace: true });
          })
          .catch((error) => {
            const errorMessage = error.message;
            addError(errorMessage);
          });
      })
      .catch((error) => {
        const errorMessage = error.message;
        addError(errorMessage);
      });
  };

  const [errors, setErrors] = useState([]);
  const addError = (error) => setErrors(errors.concat([error]));
  const onClearError = (errorToClear) => {
    const newErrors = errors.filter((error) => error !== errorToClear);
    setErrors(newErrors);
  };

  const onLogin = async ({ value }) => {
    const from = location.state?.from?.pathname || "/";
    const { email, password } = value;

    setPersistence(auth, browserSessionPersistence)
      .then(() => {
        return signInWithEmailAndPassword(auth, email, password);
      })
      .then(() => {
        navigate(from, { replace: true });
      })
      .catch((error) => {
        const errorMessage = error.message;
        addError(errorMessage);
      });
  };

  const handleSave = async ({ value }) => {
    const requestOptions = {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(value),
    };

    fetch(
      `${process.env.REACT_APP_SERVER_URL}/setNewUserAccount`,
      requestOptions
    )
      .then((response) => response.json())
      .then(() => {
        navigate("/", { replace: true });
      })
      .catch((error) => {
        console.error("Error saving signup data:", error);
        addError(i18n("failedToSaveSignupData"));
      });
  };

  const { t: i18n } = useTranslation();

  return (
    <ErrorProvider
      errors={errors}
      getErrorMessage={(error) => error}
      onClearError={onClearError}
    >
      <Box align="center">
        <Image
          src={require("../assets/CLF-logo_main_black.png")}
          height={40}
          margin={{ vertical: "medium" }}
        />
        <Box margin={{ vertical: "medium" }} align="center" elevation="medium">
          <Box
            background="white"
            pad="small"
            round="small"
            margin="medium"
            align="center"
          >
            <Heading margin="none" level={4}>
              {i18n("login")}
            </Heading>
            <Box direction="row" gap="medium" justify="center" margin="medium">
              <Button
                label={i18n("signInWithGoogle")}
                type="button"
                color="#fff"
                primary
                icon={<Google color="plain" />}
                size="large"
                onClick={signInWithGoogle}
                style={{
                  boxShadow: "black 0px 0px 0px 2px",
                }}
              />
            </Box>
            <Text>{i18n("or")}</Text>
            <Box pad="small" round="small" margin="medium" width="xmedium">
              {forgotPasswordClicked ? (
                <Form onSubmit={onForgotPassword}>
                  <FormField
                    htmlFor="email"
                    name="email"
                    label={i18n("email")}
                    validate={validateEmail.bind(null, "email")}
                  >
                    <TextInput id="email" name="email" />
                  </FormField>
                  <Box
                    direction="row"
                    gap="medium"
                    justify="center"
                    margin="medium"
                  >
                    <Button
                      type="submit"
                      primary
                      color="#C28835"
                      style={{ color: "white" }}
                      label={i18n("resetPassword")}
                    />
                    <Button
                      label={i18n("backToLogin")}
                      onClick={() => setForgotPasswordClicked(false)}
                      type="button"
                    />
                  </Box>
                </Form>
              ) : (
                <Form
                  value={value}
                  onChange={(nextValue) => setValue(nextValue)}
                  validate="submit"
                  onSubmit={onLogin}
                >
                  <FormField
                    htmlFor="email"
                    name="email"
                    label={i18n("email")}
                    validate={validateEmail.bind(null, "email")}
                  >
                    <TextInput id="email" name="email" />
                  </FormField>
                  <FormField
                    htmlFor="password"
                    name="password"
                    label={i18n("password")}
                    validate={validatePassword}
                  >
                    <TextInput type="password" id="password" name="password" />
                  </FormField>
                  <Box
                    direction="row"
                    gap="medium"
                    justify="center"
                    margin="medium"
                  >
                    <Button
                      type="submit"
                      primary
                      color="#C28835"
                      style={{ color: "white" }}
                      label={i18n("login")}
                    />
                    <Button
                      label={i18n("forgotPassword")}
                      onClick={() => setForgotPasswordClicked(true)}
                      type="button"
                    />
                  </Box>
                </Form>
              )}
            </Box>
            <Box direction="row" gap="small" justify="center">
              <Text>{i18n("notAUser")}</Text>
              <Link to="/signup">{i18n("signUp")}</Link>
            </Box>
          </Box>
        </Box>
      </Box>
    </ErrorProvider>
  );
};

export default Login;
