import React, { useState } from "react";
import { Box, Button, Form, FormField, TextInput, Heading } from "grommet";
import { getAuth, updateProfile, updatePassword } from "firebase/auth";
import { useTranslation } from "react-i18next";

const SettingsPage = () => {
  const auth = getAuth();
  const user = auth.currentUser;
  const { t } = useTranslation();

  const [name, setName] = useState(user.displayName || "");
  const [password, setPassword] = useState("");

  const handleUpdate = async () => {
    try {
      // Update the user's display name
      if (name !== user.displayName) {
        await updateProfile(user, { displayName: name });
      }
      // Update the user's password if provided
      if (password) {
        await updatePassword(user, password);
      }
      alert(t("settings_updated_successfully"));
    } catch (error) {
      alert(error.message);
    }
  };

  return (
    <Box pad="medium" width="medium" alignSelf="center">
      <Heading level="3">{t("settings")}</Heading>
      <Form>
        <FormField label={t("userName")}>
          <TextInput
            value={name}
            onChange={(event) => setName(event.target.value)}
          />
        </FormField>
        <FormField label={t("userEmail")} disabled>
          <TextInput value={user.email} disabled />
        </FormField>
        <FormField label={t("newPassword")}>
          <TextInput
            type="password"
            value={password}
            onChange={(event) => setPassword(event.target.value)}
          />
        </FormField>
        <Button label={t("update")} onClick={handleUpdate} primary />
      </Form>
    </Box>
  );
};

export default SettingsPage;
