import React from "react";
import {
  Box,
  Heading,
  Text,
  Anchor,
  Button,
  TextInput,
  FormField,
  Form,
} from "grommet";
import { MailOption } from "grommet-icons";
import { useTranslation } from "react-i18next";

const ContactUs = () => {
  const { t: i18n } = useTranslation();

  const handleSubmit = ({ value }) => {
    fetch(`${process.env.REACT_APP_SERVER_URL}/contact`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(value),
    })
      .then((response) => response.json())
      .then((data) => {
        if (data.message) {
          alert(i18n("thankYouMessage"));
        } else {
          alert(i18n("errorMessage"));
        }
      })
      .catch((error) => {
        console.error("Error submitting contact form:", error);
        alert(i18n("errorMessage"));
      });
  };

  return (
    <Box pad="large" align="center">
      <Box width="medium">
        <Heading level={2} textAlign="center" margin={{ bottom: "small" }}>
          {i18n("contactUs")}
        </Heading>
        <Text textAlign="center" margin={{ bottom: "medium" }}>
          {i18n("description")}{" "}
          <Anchor href="mailto:wave@aiglow.ai" label="wave@aiglow.ai" />.
        </Text>

        <Form onSubmit={handleSubmit}>
          <FormField
            name="name"
            htmlFor="name-input"
            label={i18n("nameLabel")}
            required
          >
            <TextInput
              id="name-input"
              name="name"
              placeholder={i18n("namePlaceholder")}
            />
          </FormField>
          <FormField
            name="email"
            htmlFor="email-input"
            label={i18n("emailLabel")}
            required
          >
            <TextInput
              id="email-input"
              name="email"
              type="email"
              placeholder={i18n("emailPlaceholder")}
            />
          </FormField>
          <FormField
            name="message"
            htmlFor="message-input"
            label={i18n("messageLabel")}
            required
          >
            <TextInput
              id="message-input"
              name="message"
              placeholder={i18n("messagePlaceholder")}
              as="textarea"
            />
          </FormField>
          <Box direction="row" justify="center" margin={{ top: "medium" }}>
            <Button
              type="submit"
              label={i18n("sendMessage")}
              icon={<MailOption />}
              primary
            />
          </Box>
        </Form>
      </Box>
    </Box>
  );
};

export default ContactUs;
