import React, { useState, useEffect } from "react";
import {
  Box,
  DataTable,
  FileInput,
  Button,
  TextInput,
  Heading,
  Text,
  Layer,
  Spinner,
  Notification,
  Tip,
} from "grommet";
import { Download, View, Upload, Info, Trash } from "grommet-icons";
import { useNavigate } from "react-router-dom";
import { useTranslation } from "react-i18next";

const Datasets = () => {
  const { t } = useTranslation(); // Initialize the translation hook
  const [datasets, setDatasets] = useState([]);
  const [search, setSearch] = useState("");
  const [uploading, setUploading] = useState(false);
  const [uploadError, setUploadError] = useState("");
  const [fileToUpload, setFileToUpload] = useState(null);
  const [deleting, setDeleting] = useState(false);
  const [deleteError, setDeleteError] = useState("");
  const [showDeleteConfirmation, setShowDeleteConfirmation] = useState(false);
  const [datasetToDelete, setDatasetToDelete] = useState(null);
  const navigate = useNavigate();

  const fetchDatasets = () => {
    fetch(`${process.env.REACT_APP_SERVER_URL}/list_datasets`)
      .then((response) => response.json())
      .then((data) => setDatasets(data))
      .catch((error) => console.error(t("errorFetchingDatasets"), error)); // Use translation key
  };

  useEffect(() => {
    fetchDatasets();
  }, []);

  const handleFileChange = (event) => {
    const file = event.target.files[0];
    if (!file) {
      setFileToUpload(null);
      return;
    }

    // Validate file type
    const validExtensions = [".csv", ".json"];
    const fileExtension = file.name.split(".").pop().toLowerCase();
    if (!validExtensions.includes(`.${fileExtension}`)) {
      setUploadError(t("invalidFileType")); // Use translation key for error message
      setFileToUpload(null);
      return;
    }

    // Validate file size
    if (file.size > 10 * 1024 * 1024) {
      setUploadError(t("fileSizeExceedsLimit")); // Use translation key
      setFileToUpload(null);
    } else {
      setFileToUpload(file);
    }
  };

  const handleFileUpload = () => {
    if (!fileToUpload) {
      setUploadError(t("noFileSelected")); // Use translation key
      return;
    }

    // Check if the file already exists in the datasets
    const isDuplicate = datasets.some(
      (dataset) =>
        dataset.name.trim().toLowerCase() ===
        fileToUpload.name
          .replace(/\.[^/.]+$/, "")
          .trim()
          .toLowerCase()
    );
    if (isDuplicate) {
      setUploadError(t("fileAlreadyExists")); // Use translation key
      setFileToUpload(null);
      return;
    }

    setUploading(true);
    setUploadError("");

    const formData = new FormData();
    formData.append("file", fileToUpload);

    fetch(`${process.env.REACT_APP_SERVER_URL}/uploaddataset`, {
      method: "POST",
      body: formData,
    })
      .then(async (response) => {
        const result = await response.json();
        if (response.ok) {
          fetchDatasets();
          setFileToUpload(null);
        } else {
          setUploadError(result.detail); // Use translation key
        }
      })
      .catch((error) => {
        console.error(t("errorUploadingFile"), error); // Use translation key
        setUploadError(t("errorUploadingFile")); // Use translation key
      })
      .finally(() => {
        setUploading(false);
      });
  };

  const handleDelete = () => {
    setDeleting(true);
    setDeleteError("");

    fetch(
      `${process.env.REACT_APP_SERVER_URL}/deletedataset/${encodeURIComponent(
        datasetToDelete
      )}`,
      {
        method: "DELETE",
      }
    )
      .then((response) => {
        if (response.ok) {
          fetchDatasets();
          setShowDeleteConfirmation(false);
        } else {
          setDeleteError(t("failedToDeleteDataset")); // Use translation key
        }
      })
      .catch((error) => {
        console.error(t("errorDeletingDataset"), error); // Use translation key
        setDeleteError(t("errorDeletingDataset")); // Use translation key
      })
      .finally(() => {
        setDeleting(false);
      });
  };

  const confirmDelete = (datasetName) => {
    setDatasetToDelete(datasetName);
    setShowDeleteConfirmation(true);
  };

  const filteredDatasets = datasets.filter((dataset) =>
    dataset.name.toLowerCase().includes(search.toLowerCase())
  );

  return (
    <Box pad="medium">
      <Heading level={2}>{t("datasets")}</Heading> {/* Use translation key */}
      <Box
        direction="row"
        gap="medium"
        align="center"
        margin={{ bottom: "medium" }}
      >
        <Box>
          <TextInput
            placeholder={t("searchDatasets")} // Use translation key
            value={search}
            onChange={(event) => setSearch(event.target.value)}
            style={{ width: "300px" }} // Adjust width as needed
          />
        </Box>

        <Box direction="row" gap="small" align="center">
          <FileInput
            name="file"
            onChange={handleFileChange}
            accept=".csv,.jsonl"
            messages={{
              browse: t("selectFile"), // Use translation key
            }}
          />

          <Button
            icon={<Upload />}
            label={uploading ? t("uploading") : t("upload")} // Use translation key
            onClick={handleFileUpload}
            disabled={uploading || !fileToUpload}
            primary
          />
          <Tip content={t("fileUploadTip")}>
            <Info />
          </Tip>
        </Box>
      </Box>
      {uploadError && (
        <Notification
          toast
          status="critical"
          title={t("uploadError")} // Use translation key
          message={uploadError}
          onClose={() => setUploadError("")}
        />
      )}
      {deleteError && (
        <Notification
          toast
          status="critical"
          title={t("deleteError")} // Use translation key
          message={deleteError}
          onClose={() => setDeleteError("")}
        />
      )}
      <DataTable
        columns={[
          {
            property: "name",
            header: <Text>{t("name")}</Text>, // Use translation key
            primary: true,
            render: (datum) => <Text truncate>{datum.name}</Text>,
          },
          {
            property: "size",
            header: <Text>{t("size")}</Text>, // Use translation key
            render: (datum) => <Text>{(datum.size / 1024).toFixed(2)} KB</Text>,
          },
          {
            property: "actions",
            header: <Text>{t("actions")}</Text>, // Use translation key
            render: (datum) => (
              <Box direction="row" gap="small">
                <Button
                  icon={<View />}
                  hoverIndicator
                  onClick={() =>
                    navigate(`/dataset/${encodeURIComponent(datum.name)}`)
                  }
                />
                <Button
                  icon={<Download />}
                  hoverIndicator
                  href={`${process.env.REACT_APP_SERVER_URL}${datum.url}`}
                  download
                />
                <Button
                  icon={<Trash />}
                  hoverIndicator
                  onClick={() => confirmDelete(datum.name)}
                />
              </Box>
            ),
          },
        ]}
        data={filteredDatasets}
        sortable
        size="medium"
      />
      {(uploading || deleting) && (
        <Layer>
          <Box
            fill
            align="center"
            justify="center"
            background={{ color: "background", opacity: "medium" }}
          >
            <Spinner size="large" />
            <Text margin={{ top: "small" }}>
              {uploading ? t("uploading") : t("deleting")}{" "}
              {/* Use translation key */}
            </Text>
          </Box>
        </Layer>
      )}
      {showDeleteConfirmation && (
        <Layer
          onEsc={() => setShowDeleteConfirmation(false)}
          onClickOutside={() => setShowDeleteConfirmation(false)}
        >
          <Box pad="medium" gap="small" width="medium">
            <Text>{t("deleteConfirmation")}</Text> {/* Use translation key */}
            <Box direction="row" gap="small" justify="end">
              <Button
                label={t("cancel")} // Use translation key
                onClick={() => setShowDeleteConfirmation(false)}
              />
              <Button
                label={t("delete")} // Use translation key
                onClick={handleDelete}
                primary
                color="status-critical"
              />
            </Box>
          </Box>
        </Layer>
      )}
    </Box>
  );
};

export default Datasets;
