/* eslint-disable no-unused-vars */
import React, { useState, useEffect } from "react";
import {
  Box,
  Button,
  Select,
  TextInput,
  Heading,
  Text,
  List,
  CheckBox,
  Anchor,
} from "grommet";

import { useTranslation } from "react-i18next";

const ModelOperations = () => {
  const { t: i18n } = useTranslation();
  const [savedModels, setSavedModels] = useState([]);
  const [uploadedDatasets, setUploadedDatasets] = useState([]);
  const [quantizationDatasets, setQuantizationDatasets] = useState([
    "wikitext2",
    "c4",
    "c4-new",
  ]);
  const [selectedModel, setSelectedModel] = useState("");
  const [selectedDataset, setSelectedDataset] = useState("");
  const [selectedQuantizationDataset, setSelectedQuantizationDataset] =
    useState("");
  const [operationTypes, setOperationTypes] = useState({
    Training: false,
    FineTuning: false,
    Quantization: false,
    ProdModel: false,
  });

  const [epochs, setEpochs] = useState(10);
  const [learningRate, setLearningRate] = useState(0.001);
  const [batchSize, setBatchSize] = useState(32);
  const [usePretrained, setUsePretrained] = useState(false);

  // Fine-tuning specific
  const [bf16, setBf16] = useState(false);
  const [saveSteps, setSaveSteps] = useState(200);
  const [gradientAccumulationSteps, setGradientAccumulationSteps] = useState(1);
  const [targetModules, setTargetModules] = useState("all-linear");

  // New Fine-tuning parameters
  const [attnImplementation, setAttnImplementation] = useState("eager");
  const [saveTotalLimit, setSaveTotalLimit] = useState(3);
  const [seed, setSeed] = useState(0);

  // Advanced settings
  const [advancedSettings, setAdvancedSettings] = useState(false);
  const [gradientCheckpointing, setGradientCheckpointing] = useState(true);
  const [r, setR] = useState(16);
  const [loraAlpha, setLoraAlpha] = useState(32);
  const [loraDropout, setLoraDropout] = useState(0.05);

  // Quantization specific
  const [quantizationBits, setQuantizationBits] = useState(8);

  const [statusMessage, setStatusMessage] = useState("");
  const [isProcessing, setIsProcessing] = useState(false);

  // Lists for the models
  const [processedModels, setProcessedModels] = useState([]);

  useEffect(() => {
    const fetchStatus = () => {
      fetch(`${process.env.REACT_APP_SERVER_URL}/status`)
        .then((response) => response.json())
        .then((data) => {
          setIsProcessing(data.is_training); // Update isTraining state
        })
        .catch((error) => console.error("Error fetching status:", error));
    };

    // Fetch status immediately when the component loads
    fetchStatus();

    // Poll the status every 5 seconds after the initial fetch
    const interval = setInterval(fetchStatus, 5000);

    // Clean up interval on unmount
    return () => clearInterval(interval);
  }, []);

  useEffect(() => {
    const fetchStatus = () => {
      fetch(`${process.env.REACT_APP_SERVER_URL}/status`)
        .then((response) => response.json())
        .then((data) => {
          setIsProcessing(data.is_training); // Update isTraining state
        })
        .catch((error) => console.error("Error fetching status:", error));
    };

    // Fetch status immediately when the component loads
    fetchStatus();

    // Poll the status every 5 seconds after the initial fetch
    const interval = setInterval(fetchStatus, 5000);

    // Clean up interval on unmount
    return () => clearInterval(interval);
  }, []);

  useEffect(() => {
    fetchSavedModels();
    fetchUploadedDatasets();
    fetchProcessedModels();
  }, []);

  const fetchSavedModels = () => {
    fetch(`${process.env.REACT_APP_SERVER_URL}/list_saved_models`)
      .then((response) => response.json())
      .then((data) => setSavedModels(data))
      .catch((error) => console.error("Error fetching saved models:", error));
  };

  const fetchUploadedDatasets = () => {
    fetch(`${process.env.REACT_APP_SERVER_URL}/list_uploaded_datasets`)
      .then((response) => response.json())
      .then((data) => setUploadedDatasets(data))
      .catch((error) =>
        console.error("Error fetching uploaded datasets:", error)
      );
  };

  const fetchProcessedModels = () => {
    fetch(`${process.env.REACT_APP_SERVER_URL}/list_processed_models`)
      .then((response) => response.json())
      .then((data) => setProcessedModels(data))
      .catch((error) =>
        console.error("Error fetching processed models:", error)
      );
  };

  const handleOperation = () => {
    if (!selectedModel.trim() || !selectedDataset.trim()) {
      alert(i18n("select_model_and_dataset"));
      return;
    }

    const selectedOperations = Object.keys(operationTypes).filter(
      (key) => operationTypes[key]
    );

    if (selectedOperations.length === 0) {
      alert(i18n("select_at_least_one_operation"));
      return;
    }

    setIsProcessing(true);
    setStatusMessage(i18n("processing"));

    selectedOperations.forEach((operationType) => {
      let body = {
        model_name: selectedModel,
        dataset_path: selectedDataset,
        operation: operationType,
      };

      if (operationType === "Training") {
        body = {
          ...body,
          num_train_epochs: epochs,
          learning_rate: learningRate,
          per_device_eval_batch_size: batchSize,
          per_device_train_batch_size: batchSize,
          use_pretrained: usePretrained,
          training: true,
        };
      }

      if (operationType === "FineTuning") {
        body = {
          ...body,
          bf16,
          save_steps: saveSteps,
          gradient_accumulation_steps: gradientAccumulationSteps,
          target_modules: targetModules,
          attn_implementation: attnImplementation,
          save_total_limit: saveTotalLimit,
          seed: seed,
          gradient_checkpointing: gradientCheckpointing,
          r: r,
          lora_alpha: loraAlpha,
          lora_dropout: loraDropout,
          finetuning: true,
        };
      }

      if (operationType === "Quantization") {
        body = {
          ...body,
          quantization_bits: quantizationBits,
          dataset_id: selectedQuantizationDataset,
          quantization: true,
        };
      }

      if (operationType === "ProdModel") {
        body = {
          ...body,
          onnx: true,
        };
      }

      fetch(`${process.env.REACT_APP_SERVER_URL}/process_model`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(body),
      })
        .then((response) => response.json())
        .then((data) => {
          setStatusMessage(data.message);
          fetchProcessedModels();
        })
        .catch((error) => {
          console.error("Error during operation:", error);
          setStatusMessage(i18n("error_processing"));
        })
        .finally(() => setIsProcessing(false));
    });
  };

  const handleCheckBoxChange = (operation) => {
    setOperationTypes((prev) => {
      const newOperations = { ...prev, [operation]: !prev[operation] };
      if (operation === "Quantization" && newOperations[operation]) {
        setEpochs(1);
        setBatchSize(1);
      } else if (operation !== "Quantization" && !newOperations[operation]) {
        fetchUploadedDatasets();
      }
      return newOperations;
    });
  };

  return (
    <Box justify="left" pad="large" overflow="auto">
      <Box flex={{ shrink: 0 }}>
        <Heading level={2} textAlign="left">
          {i18n("model_operations")}
        </Heading>

        {/* Select Model */}
        <Box margin={{ bottom: "medium" }}>
          <Select
            options={savedModels}
            value={selectedModel}
            placeholder={i18n("select_model")}
            onChange={({ option }) => setSelectedModel(option)}
            disabled={isProcessing}
          />
        </Box>

        {/* Select Dataset */}
        <Box margin={{ bottom: "medium" }}>
          <Select
            options={uploadedDatasets}
            value={selectedDataset}
            placeholder={i18n("select_dataset")}
            onChange={({ option }) => setSelectedDataset(option)}
            disabled={isProcessing}
          />
        </Box>

        {/* Select Operations */}
        <Box margin={{ bottom: "medium" }} gap="small">
          <CheckBox
            checked={operationTypes.Training}
            label={i18n("Training")}
            onChange={() => handleCheckBoxChange("Training")}
            disabled={isProcessing}
          />
          <CheckBox
            checked={operationTypes.FineTuning}
            label={i18n("FineTuning")}
            onChange={() => handleCheckBoxChange("FineTuning")}
            disabled={isProcessing}
          />
          <CheckBox
            checked={operationTypes.Quantization}
            label={i18n("Quantization")}
            onChange={() => handleCheckBoxChange("Quantization")}
            disabled={isProcessing}
          />
          <CheckBox
            checked={operationTypes.ProdModel}
            label={i18n("ProdModel")}
            onChange={() => handleCheckBoxChange("ProdModel")}
            disabled={isProcessing}
          />
          <Text>
            {i18n("to_know_about_parameters")}{" "}
            <Anchor label="click here" href="/ModelOperationsHelp">
              click here
            </Anchor>
          </Text>
        </Box>
        {/* Show Training specific parameters */}
        {operationTypes.Training && (
          <Box>
            <Heading level={3}>{i18n("training_parameters")}</Heading>
            <Box gap="medium">
              <Box>
                <Text>{i18n("num_train_epochs")}</Text>
                <TextInput
                  type="number"
                  placeholder={i18n("eg_10")}
                  value={epochs}
                  onChange={(e) => setEpochs(Number(e.target.value))}
                  disabled={isProcessing}
                />
              </Box>

              <Box>
                <Text>{i18n("learning_rate")}</Text>
                <TextInput
                  type="number"
                  step="0.001"
                  placeholder={i18n("eg_0.001")}
                  value={learningRate}
                  onChange={(e) => setLearningRate(Number(e.target.value))}
                  disabled={isProcessing}
                />
              </Box>

              <Box>
                <Text>{i18n("per_device_eval_batch_size")}</Text>
                <TextInput
                  type="number"
                  placeholder={i18n("eg_32")}
                  value={batchSize}
                  onChange={(e) => setBatchSize(Number(e.target.value))}
                  disabled={isProcessing}
                />
              </Box>

              <Box>
                <Text>{i18n("per_device_train_batch_size")}</Text>
                <TextInput
                  type="number"
                  placeholder={i18n("eg_32")}
                  value={batchSize}
                  onChange={(e) => setBatchSize(Number(e.target.value))}
                  disabled={isProcessing}
                />
              </Box>

              <Box>
                <CheckBox
                  checked={usePretrained}
                  label={i18n("use_pretrained")}
                  onChange={(event) => setUsePretrained(event.target.checked)}
                  disabled={isProcessing}
                />
              </Box>
            </Box>
          </Box>
        )}
        {/* Show Fine-tuning specific parameters */}
        {operationTypes.FineTuning && (
          <Box>
            <Heading level={3}>{i18n("fine_tuning_parameters")}</Heading>
            <Box gap="medium">
              <Box
                direction="row"
                gap="medium"
                align="center"
                margin={{ bottom: "medium" }}
              >
                <Text>{i18n("save_steps")}</Text>
                <TextInput
                  type="number"
                  placeholder={i18n("eg_200")}
                  value={saveSteps}
                  onChange={(e) => setSaveSteps(Number(e.target.value))}
                  disabled={isProcessing}
                />
              </Box>
              <Box
                direction="row"
                gap="medium"
                align="center"
                margin={{ bottom: "medium" }}
              >
                <Text>{i18n("gradient_accumulation_steps")}</Text>
                <TextInput
                  type="number"
                  placeholder={i18n("eg_1")}
                  value={gradientAccumulationSteps}
                  onChange={(e) =>
                    setGradientAccumulationSteps(Number(e.target.value))
                  }
                  disabled={isProcessing}
                />
              </Box>

              <Box
                direction="row"
                gap="medium"
                align="center"
                margin={{ bottom: "medium" }}
              >
                <Text>{i18n("target_modules")}</Text>
                <TextInput
                  placeholder={i18n("eg_all_linear")}
                  value={targetModules}
                  onChange={(e) => setTargetModules(e.target.value)}
                  disabled={isProcessing}
                />
              </Box>

              <Box
                direction="row"
                gap="medium"
                align="center"
                margin={{ bottom: "medium" }}
              >
                <Text>{i18n("attention_implementation")}</Text>
                <Select
                  options={["eager", "flash_attention_2"]}
                  value={attnImplementation}
                  onChange={({ option }) => setAttnImplementation(option)}
                  disabled={isProcessing}
                />
              </Box>

              <Box
                direction="row"
                gap="medium"
                align="center"
                margin={{ bottom: "medium" }}
              >
                <Text>{i18n("save_total_limit")}</Text>
                <TextInput
                  type="number"
                  placeholder={i18n("eg_3")}
                  value={saveTotalLimit}
                  onChange={(e) => setSaveTotalLimit(Number(e.target.value))}
                  disabled={isProcessing}
                />
              </Box>

              <Box
                direction="row"
                gap="medium"
                align="center"
                margin={{ bottom: "medium" }}
              >
                <Text>{i18n("seed")}</Text>
                <TextInput
                  type="number"
                  placeholder={i18n("eg_0")}
                  value={seed}
                  onChange={(e) => setSeed(Number(e.target.value))}
                  disabled={isProcessing}
                />
              </Box>

              <Box
                direction="row"
                gap="medium"
                align="center"
                margin={{ bottom: "medium" }}
              >
                <CheckBox
                  checked={bf16}
                  label={i18n("use_bf16")}
                  onChange={(event) => setBf16(event.target.checked)}
                  disabled={isProcessing}
                />
              </Box>

              {/* Advanced Settings Toggle */}
              <Box
                direction="row"
                gap="medium"
                align="center"
                margin={{ bottom: "medium" }}
              >
                <CheckBox
                  checked={advancedSettings}
                  label={i18n("advanced_settings")}
                  onChange={() => setAdvancedSettings(!advancedSettings)}
                  disabled={isProcessing}
                />
              </Box>

              {/* Show Advanced Parameters */}
              {advancedSettings && (
                <Box margin={{ top: "medium" }}>
                  <Box
                    direction="row"
                    gap="medium"
                    align="center"
                    margin={{ bottom: "medium" }}
                  >
                    <CheckBox
                      checked={gradientCheckpointing}
                      label={i18n("gradient_checkpointing")}
                      onChange={(event) =>
                        setGradientCheckpointing(event.target.checked)
                      }
                      disabled={isProcessing}
                    />
                  </Box>
                  <Box
                    direction="row"
                    gap="medium"
                    align="center"
                    margin={{ bottom: "medium" }}
                  >
                    <Text>{i18n("r")}</Text>
                    <TextInput
                      type="number"
                      value={r}
                      onChange={(e) => setR(Number(e.target.value))}
                      disabled={isProcessing}
                    />
                  </Box>
                  <Box
                    direction="row"
                    gap="medium"
                    align="center"
                    margin={{ bottom: "medium" }}
                  >
                    <Text>{i18n("lora_alpha")}</Text>
                    <TextInput
                      type="number"
                      value={loraAlpha}
                      onChange={(e) => setLoraAlpha(Number(e.target.value))}
                      disabled={isProcessing}
                    />
                  </Box>
                  <Box
                    direction="row"
                    gap="medium"
                    align="center"
                    margin={{ bottom: "medium" }}
                  >
                    <Text>{i18n("lora_dropout")}</Text>
                    <TextInput
                      type="number"
                      value={loraDropout}
                      onChange={(e) => setLoraDropout(Number(e.target.value))}
                      disabled={isProcessing}
                    />
                  </Box>
                </Box>
              )}
            </Box>
          </Box>
        )}

        {/* Show Quantization specific parameters */}
        {operationTypes.Quantization && (
          <Box>
            <Heading level={3}>{i18n("quantization_parameters")}</Heading>
            <Box gap="medium">
              {/* Dropdown for quantization-specific datasets */}
              <Box margin={{ top: "medium" }}>
                <Select
                  options={quantizationDatasets}
                  value={selectedQuantizationDataset}
                  placeholder={i18n("select_quantization_dataset")}
                  onChange={({ option }) =>
                    setSelectedQuantizationDataset(option)
                  }
                  disabled={isProcessing}
                />
              </Box>
              <Box>
                <Text>{i18n("quantization_bits")}</Text>
                <TextInput
                  type="number"
                  placeholder={i18n("eg_8")}
                  value={quantizationBits}
                  onChange={(e) => setQuantizationBits(Number(e.target.value))}
                  disabled={isProcessing}
                />
              </Box>
            </Box>
          </Box>
        )}

        {/* Process Button */}
        <Box margin={{ top: "medium" }}>
          <Button
            label={isProcessing ? i18n("processing") : i18n("process")}
            onClick={handleOperation}
            disabled={
              isProcessing ||
              !selectedModel ||
              !selectedDataset ||
              !Object.values(operationTypes).includes(true) ||
              (operationTypes.Quantization && !selectedQuantizationDataset)
            }
            primary
          />
        </Box>

        {/* Status Message */}
        {statusMessage && (
          <Box margin={{ top: "medium" }}>
            <Text>{statusMessage}</Text>
          </Box>
        )}

        {/* List of Processed Models */}
        <Box margin={{ top: "large" }}>
          <Heading level={3}>{i18n("processed_models")}</Heading>
          <List
            data={processedModels}
            primaryKey="file_name"
            secondaryKey="operation"
          />
        </Box>
      </Box>
    </Box>
  );
};

export default ModelOperations;
