//test validation for various types of inputs
export const validateEmail = (label, email) => {
  if (!email) {
    return "Email is empty";
  }
  const emailRegExp =
    /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:.[a-zA-Z0-9-]+)*$/;
  if (!emailRegExp.test(email)) {
    return "Invalid/empty email address";
  }
};

export const validatePassword = (password) => {
  if (!password) {
    return "Password is empty";
  }
};

export const validateConfirmedPassword = (
  password,
  { confirmPassword: confirmedPassword }
) => {
  if (password !== confirmedPassword) {
    return "Passwords don't match";
  }
};

export const validateFirstName = (name) => {
  if (!name) {
    return "First name is empty";
  }
};

export const validateLastName = (name) => {
  if (!name) {
    return "Last name is empty";
  }
};

export const validatePhoneNumber = (phone) => {
  const phoneRegExp = /^\+(?:[0-9] ?){6,14}[0-9]$/;
  if (phone && !phoneRegExp.test(phone)) {
    return "Invalid phone number";
  }
};

export const stringNotEmpty = (label, str) => {
  if (!str) {
    return `${label} is empty`;
  }
};

export const validateAge = (age) => {
  if (
    age &&
    (!Number.isInteger(Number(age)) || Number(age) < 10 || Number(age) > 100)
  ) {
    return "Age should be between 10 and 100";
  }
};

export const validateNumberInput = (label, input) => {
  const numberRegExp = /^[0-9]+$/;
  if (!input) {
    return `${label} is empty`;
  }
  if (!numberRegExp.test(input)) {
    return "Input should contain only numbers";
  }
};

export const validateUrl = (label, url) => {
  if (!url) {
    return `${label} is empty`;
  }
  const urlRegExp =
    /^((ftp|https|https):\/\/)?(www.)?(?!.*(ftp|http|https|www.))[a-zA-Z0-9_-]+(\.[a-zA-Z]+)+((\/)[\w#]+)*(\/\w+\?[a-zA-Z0-9_]+=\w+(&[a-zA-Z0-9_]+=\w+)*)?\/?$/gm;
  if (!urlRegExp.test(url)) {
    return "Invalid/empty URL format";
  }
};
