import React, { useState, useEffect } from "react";
import { Box, Button, TextInput, Heading, Text, List, Select } from "grommet";
import { useTranslation } from "react-i18next";

const ModelQuantization = () => {
  const { t: i18n } = useTranslation();
  const [savedModels, setSavedModels] = useState([]);
  const [selectedModel, setSelectedModel] = useState("");
  const [quantizationBits, setQuantizationBits] = useState(8); // Default to 8-bit quantization
  const [isQuantizing, setIsQuantizing] = useState(false);
  const [statusMessage, setStatusMessage] = useState("");
  const [quantizedModels, setQuantizedModels] = useState([]);

  useEffect(() => {
    fetchSavedModels();
    fetchQuantizedModels();
  }, []);

  const fetchSavedModels = () => {
    fetch(`${process.env.REACT_APP_SERVER_URL}/list_saved_models`)
      .then((response) => response.json())
      .then((data) => setSavedModels(data))
      .catch((error) => console.error("Error fetching saved models:", error));
  };

  const fetchQuantizedModels = () => {
    fetch(`${process.env.REACT_APP_SERVER_URL}/list_quantized_models`)
      .then((response) => response.json())
      .then((data) => setQuantizedModels(data))
      .catch((error) =>
        console.error("Error fetching quantized models:", error)
      );
  };

  const handleQuantization = () => {
    if (!selectedModel.trim()) {
      alert(i18n("select_model_to_quantize"));
      return;
    }

    setIsQuantizing(true);
    setStatusMessage(i18n("quantizing_model"));

    const body = {
      model_file_name: selectedModel,
      quantization_bits: quantizationBits,
    };

    fetch(`${process.env.REACT_APP_SERVER_URL}/quantize_model`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(body),
    })
      .then((response) => response.json())
      .then((data) => {
        setStatusMessage(data.message);
        setIsQuantizing(false);
        fetchQuantizedModels(); // Refresh the list of quantized models
      })
      .catch((error) => {
        console.error("Error quantizing model:", error);
        setStatusMessage(i18n("error_quantizing_model"));
        setIsQuantizing(false);
      });
  };

  return (
    <Box justify="left" pad="large">
      <Heading level={2} textAlign="center">
        {i18n("model_quantization")}
      </Heading>

      <Box margin={{ bottom: "medium" }}>
        <Select
          options={savedModels} // Populate dropdown with saved models
          value={selectedModel}
          placeholder={i18n("select_model_to_quantize")}
          onChange={({ option }) => setSelectedModel(option)}
          disabled={isQuantizing}
        />
      </Box>

      <Box margin={{ bottom: "medium" }}>
        <Text margin={{ bottom: "xsmall" }}>{i18n("quantization_bits")}</Text>
        <TextInput
          type="number"
          placeholder={i18n("eg_8")}
          value={quantizationBits}
          onChange={(e) => setQuantizationBits(Number(e.target.value))}
          disabled={isQuantizing}
        />
      </Box>

      <Box direction="row">
        <Button
          label={isQuantizing ? i18n("quantizing") : i18n("start_quantization")}
          onClick={handleQuantization}
          primary
          disabled={isQuantizing}
        />
      </Box>

      {statusMessage && (
        <Text margin={{ top: "medium" }} color="status-critical">
          {statusMessage}
        </Text>
      )}

      <Box>
        <Heading level={3} margin={{ top: "large", bottom: "small" }}>
          {i18n("quantized_models")}
        </Heading>
        <List
          data={quantizedModels}
          primaryKey={(model) => (
            <a
              href={`${process.env.REACT_APP_SERVER_URL}/download_quantized_model/${model}`}
              download
            >
              {model}
            </a>
          )}
        />
      </Box>
    </Box>
  );
};

export default ModelQuantization;
