import React, { useState, useEffect } from "react";
import {
  Box,
  Button,
  TextInput,
  Heading,
  Text,
  List,
  Select,
  CheckBox,
} from "grommet";
import { useTranslation } from "react-i18next";

const ModelTraining = () => {
  const { t: i18n } = useTranslation();
  const [savedModels, setSavedModels] = useState([]);
  const [uploadedDatasets, setUploadedDatasets] = useState([]);
  const [selectedModel, setSelectedModel] = useState("");
  const [selectedDataset, setSelectedDataset] = useState("");
  const [epochs, setEpochs] = useState(10);
  const [learningRate, setLearningRate] = useState(0.001);
  const [batchSize, setBatchSize] = useState(32);
  const [isTraining, setIsTraining] = useState(false);
  const [statusMessage, setStatusMessage] = useState("");
  const [trainedModels, setTrainedModels] = useState([]);
  const [usePretrained, setUsePretrained] = useState(false);

  useEffect(() => {
    fetchSavedModels();
    fetchUploadedDatasets();
    fetchTrainedModels();
  }, []);

  const fetchSavedModels = () => {
    fetch(`${process.env.REACT_APP_SERVER_URL}/list_saved_models`)
      .then((response) => response.json())
      .then((data) => setSavedModels(data))
      .catch((error) => console.error("Error fetching saved models:", error));
  };

  const fetchUploadedDatasets = () => {
    fetch(`${process.env.REACT_APP_SERVER_URL}/list_uploaded_datasets`)
      .then((response) => response.json())
      .then((data) => setUploadedDatasets(data))
      .catch((error) =>
        console.error("Error fetching uploaded datasets:", error)
      );
  };

  const fetchTrainedModels = () => {
    fetch(`${process.env.REACT_APP_SERVER_URL}/list_trained_models`)
      .then((response) => response.json())
      .then((data) => setTrainedModels(data))
      .catch((error) => console.error("Error fetching trained models:", error));
  };

  const handleTraining = () => {
    if (!selectedModel.trim() || !selectedDataset.trim()) {
      alert(i18n("select_model_and_dataset"));
      return;
    }

    setIsTraining(true);
    setStatusMessage(i18n("training_model"));

    const body = {
      model_name: selectedModel,
      dataset_path: selectedDataset,
      num_train_epochs: epochs,
      learning_rate: learningRate,
      per_device_eval_batch_size: batchSize,
      per_device_train_batch_size: batchSize,
      production_training: usePretrained,
    };

    fetch(`${process.env.REACT_APP_SERVER_URL}/train_model`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(body),
    })
      .then((response) => response.json())
      .then((data) => {
        setStatusMessage(data.message);
        setIsTraining(false);
        fetchTrainedModels();
      })
      .catch((error) => {
        console.error("Error training model:", error);
        setStatusMessage(i18n("error_training_model"));
        setIsTraining(false);
      });
  };

  return (
    <Box justify="left" pad="large">
      <Heading level={2} textAlign="center">
        {i18n("model_training")}
      </Heading>

      <Box margin={{ bottom: "medium" }}>
        <Select
          options={savedModels}
          value={selectedModel}
          placeholder={i18n("select_model_to_train")}
          onChange={({ option }) => setSelectedModel(option)}
          disabled={isTraining}
        />
      </Box>

      <Box margin={{ bottom: "medium" }}>
        <Select
          options={uploadedDatasets}
          value={selectedDataset}
          placeholder={i18n("select_dataset")}
          onChange={({ option }) => setSelectedDataset(option)}
          disabled={isTraining}
        />
      </Box>

      <Box margin={{ bottom: "medium" }} gap="medium">
        <Box>
          <Text margin={{ bottom: "xsmall" }}>{i18n("epochs")}</Text>
          <TextInput
            type="number"
            placeholder={i18n("e.g_10")}
            value={epochs}
            onChange={(e) => setEpochs(Number(e.target.value))}
            disabled={isTraining}
          />
        </Box>
        <Box>
          <Text margin={{ bottom: "xsmall" }}>{i18n("learning_rate")}</Text>
          <TextInput
            type="number"
            step="0.0001"
            placeholder={i18n("e.g_0_001")}
            value={learningRate}
            onChange={(e) => setLearningRate(Number(e.target.value))}
            disabled={isTraining}
          />
        </Box>
        <Box>
          <Text margin={{ bottom: "xsmall" }}>{i18n("batch_size")}</Text>
          <TextInput
            type="number"
            placeholder={i18n("e.g_32")}
            value={batchSize}
            onChange={(e) => setBatchSize(Number(e.target.value))}
            disabled={isTraining}
          />
        </Box>
        <Box>
          <CheckBox
            checked={usePretrained}
            label={i18n("use_pretrained_weights")}
            onChange={(event) => setUsePretrained(event.target.checked)}
            disabled={isTraining}
          />
        </Box>
      </Box>

      <Box direction="row">
        <Button
          label={isTraining ? i18n("training") : i18n("start_training")}
          onClick={handleTraining}
          primary
          disabled={isTraining}
        />
      </Box>

      {statusMessage && (
        <Text margin={{ top: "medium" }} color="status-critical">
          {statusMessage}
        </Text>
      )}

      <Box>
        <Heading level={3} margin={{ top: "large", bottom: "small" }}>
          {i18n("trained_models")}
        </Heading>
        <List
          data={trainedModels}
          primaryKey={(model) => (
            <a
              href={`${process.env.REACT_APP_SERVER_URL}/download_trained_model/${model}`}
              download
            >
              {model}
            </a>
          )}
        />
      </Box>
    </Box>
  );
};

export default ModelTraining;
