import i18n from "i18next";
import { initReactI18next } from "react-i18next";
import i18nEnglishTranslations from "./i18nEnglishTranslations";
import i18nJapaneseTranslations from "./i18nJapaneseTranslations";
import i18nChineseTranslations from "./i18nChineseTranslations";

/*
This code initializes and configures the i18next library for internationalization (i18n) in a React application, 
setting up translation resources for English and Japanese languages and defining the default language as English.
*/
i18n
  .use(initReactI18next) // passes i18n down to react-i18next
  .init({
    // the translations
    // (tip move them in a JSON file and import them,
    // or even better, manage them via a UI: https://react.i18next.com/guides/multiple-translation-files#manage-your-translations-with-a-management-gui)
    resources: {
      en: {
        translation: i18nEnglishTranslations,
      },
      ja: {
        translation: i18nJapaneseTranslations,
      },
      cn: {
        translation: i18nChineseTranslations,
      },
    },
    lng: "en", // if you're using a language detector, do not define the lng optionx`x
    fallbackLng: "en",

    interpolation: {
      escapeValue: false, // react already safes from xss => https://www.i18next.com/translation-function/interpolation#unescape
    },
  });

export default i18n;
