import { React, useState, useEffect } from "react";
import { Box, Text, Accordion, AccordionPanel, Heading } from "grommet";
import { useTranslation } from "react-i18next";
import AppBreadcrumbs from "./AppBreadcrumbs";

function Help() {
  const { t: i18n } = useTranslation();
  const hierarchy = [
    {
      label: i18n("dashboard"),
      link: "/",
    },
    {
      label: i18n("help"),
      link: "/help",
    },
  ];

  const [datasets, setDatasets] = useState([]);

  useEffect(() => {
    // Fetch datasets from the server
    fetch(`${process.env.REACT_APP_SERVER_URL}/files`)
      .then((response) => {
        if (!response.ok) {
          throw new Error("Network response was not ok");
        }
        return response.json();
      })
      .then((data) => setDatasets(data))
      .catch((error) => console.error(i18n("errorFetchingDatasets"), error)); // Use translation key
  }, [i18n]);

  return (
    <Box margin="small" overflow="auto">
      <AppBreadcrumbs hierarchy={hierarchy} />
      <Box pad="medium">
        <Heading level="2">{i18n("faqHeading")}</Heading>
        <Accordion>
          <AccordionPanel label={i18n("faq.localLLMs")}>
            <Box pad="medium" background="light-2">
              <Text>{i18n("faq.localLLMsDesc")}</Text>
            </Box>
          </AccordionPanel>

          <AccordionPanel label={i18n("faq.whichModel")}>
            <Box pad="medium" background="light-2">
              <Text>{i18n("faq.whichModelDesc")}</Text>
            </Box>
          </AccordionPanel>

          <AccordionPanel label={i18n("faq.quantization")}>
            <Box pad="medium" background="light-2">
              <Text>{i18n("faq.quantizationDesc")}</Text>
            </Box>
          </AccordionPanel>

          <AccordionPanel label={i18n("faq.runCode")}>
            <Box pad="medium" background="light-2">
              <Text>{i18n("faq.runCodeDesc")}</Text>
            </Box>
          </AccordionPanel>

          <AccordionPanel label={i18n("faq.baseVsChat")}>
            <Box pad="medium" background="light-2">
              <Text>{i18n("faq.baseVsChatDesc")}</Text>
            </Box>
          </AccordionPanel>

          <AccordionPanel label={i18n("faq.training")}>
            <Box pad="medium" background="light-2">
              <Text>{i18n("faq.trainingDesc")}</Text>
            </Box>
          </AccordionPanel>

          <AccordionPanel label={i18n("faq.afterDownload")}>
            <Box pad="medium" background="light-2">
              <Text>{i18n("faq.afterDownloadDesc")}</Text>
            </Box>
          </AccordionPanel>

          <AccordionPanel label={i18n("faq.manageDatasets")}>
            <Box pad="medium" background="light-2">
              <Text>{i18n("faq.manageDatasetsDesc")}</Text>
            </Box>
          </AccordionPanel>

          <AccordionPanel label={i18n("faq.publishAPK")}>
            <Box pad="medium" background="light-2">
              <Text>{i18n("faq.publishAPKDesc")}</Text>
            </Box>
          </AccordionPanel>

          <AccordionPanel label={i18n("faq.chatsInLLMs")}>
            <Box pad="medium" background="light-2">
              <Text>{i18n("faq.chatsInLLMsDesc")}</Text>
            </Box>
          </AccordionPanel>
          <AccordionPanel label={i18n("sampleDatasets")}>
            <Box pad="medium" background="light-2">
              <Text>{i18n("get the sample files here.")}</Text>
              <Box direction="row" gap="small" margin={{ top: "small" }}>
                {datasets.length > 0 ? (
                  datasets.map((dataset) => (
                    <a
                      key={dataset.name}
                      href={`${process.env.REACT_APP_SERVER_URL}/files/${dataset.name}`}
                      download
                    >
                      <Text>{i18n(`Download ${dataset.name}`)}</Text>
                    </a>
                  ))
                ) : (
                  <Text>{i18n("No datasets available.")}</Text> // Message if no datasets are available
                )}
              </Box>
            </Box>
          </AccordionPanel>
        </Accordion>
      </Box>
    </Box>
  );
}

export default Help;
