import React, { useEffect, useState } from "react";
import { useNavigate, useLocation } from "react-router";
import { getAuth, onAuthStateChanged } from "firebase/auth";
import { Spinner } from "grommet";
import UserContext from "./UserContext";

/*
Wraps its child components and ensures that the user is authenticated before rendering them.
It uses Firebase Authentication to check if a user is logged in.
If the user is not logged in, it redirects them to the login page.
*/
const RequireAuth = ({ children }) => {
  const [userLoaded, setUserLoaded] = useState(false);
  const navigate = useNavigate();
  const location = useLocation();

  useEffect(() => {
    const auth = getAuth();

    onAuthStateChanged(auth, (user) => {
      if (!user) {
        navigate("/login", { from: location.pathname });
      } else {
        setUserLoaded(true);
      }
    });
  }, []);

  if (!userLoaded) {
    return <Spinner />;
  }

  return (
    <UserContext.Provider value={{ user: getAuth().currentUser }}>
      {children}
    </UserContext.Provider>
  );
};

export default RequireAuth;
