import React, { useEffect } from "react";
import { Route, Routes } from "react-router-dom";

import "./App.css";
import Login from "./components/Login";
import Signup from "./components/Signup";
import Layout from "./components/Layout";
import Dashboard from "./components/Dashboard";
import ModelManagement from "./components/ModelManagement";
import ModelTraining from "./components/ModelTraining";
import ModelFineTuning from "./components/ModelFineTuning";
import Chats from "./components/Chats";
import ModelOperations from "./components/ModelOperations";
import ModelQuantization from "./components/ModelQuantization";
import ModelSelectionChats from "./components/ModelSelectionChats";
import Help from "./components/Help";
import Datasets from "./components/Datasets";
import RequireAuth from "./components/RequireAuth";
import NotFound from "./components/NotFound";
import DatasetDetail from "./components/DatasetDetail";
// import PublishModelAPK from "./components/PublishModelAPK";
import i18n from "./translations/i18n";
import ContactUs from "./components/ContactUs";
import SettingsPage from "./components/SettingsPage";
import ModelOperationsHelp from "./components/ModelOperationsHelp";

//The main component of a React application, setting up the routes and components for different pages,
//including authentication requirements, localization settings, and a default "Not Found" page

function App() {
  useEffect(() => {
    const language = window.localStorage.getItem("appLanguage");
    if (language) {
      i18n.changeLanguage(language);
    }
  }, []);

  return (
    <Routes>
      <Route path="/login" element={<Login />} />
      <Route path="/signup" element={<Signup />} />
      <Route
        path="/"
        element={
          <RequireAuth>
            <Layout />
          </RequireAuth>
        }
      >
        <Route index element={<Dashboard />} />
        <Route path="dashboard" element={<Dashboard />} />
        <Route path="modelFineTuning" element={<ModelFineTuning />} />
        <Route path="modelManagement" element={<ModelManagement />} />
        <Route path="modelTraining" element={<ModelTraining />} />
        <Route path="Chats" element={<Chats />} />
        <Route path="modelSelectionChats" element={<ModelSelectionChats />} />
        <Route path="modelQuantization" element={<ModelQuantization />} />
        <Route path="datasets" element={<Datasets />} />
        <Route path="/dataset/:filename" element={<DatasetDetail />} />
        <Route path="help" element={<Help />} />
        {/* <Route path="publishModelAPK" element={<PublishModelAPK />} /> */}
        <Route path="modelOperations" element={<ModelOperations />} />
        <Route path="contactUs" element={<ContactUs />} />
        <Route path="settingsPage" element={<SettingsPage />} />
        <Route path="modelOperationsHelp" element={<ModelOperationsHelp />} />
      </Route>
      <Route path="*" element={<NotFound />} />
    </Routes>
  );
}

export default App;
