import React, { useState, useEffect } from "react";
import {
  Box,
  Button,
  TextInput,
  Heading,
  Text,
  List,
  Select,
  CheckBox,
} from "grommet";
import { useTranslation } from "react-i18next";

const ModelFineTuning = () => {
  const { t: i18n } = useTranslation();
  const [savedModels, setSavedModels] = useState([]);
  const [uploadedDatasets, setUploadedDatasets] = useState([]);
  const [selectedModel, setSelectedModel] = useState("");
  const [selectedDataset, setSelectedDataset] = useState("");
  const [epochs, setEpochs] = useState(10);
  const [learningRate, setLearningRate] = useState(0.001);
  const [batchSize, setBatchSize] = useState(32);
  const [isFineTuning, setIsFineTuning] = useState(false);
  const [statusMessage, setStatusMessage] = useState("");
  const [fineTunedModels, setFineTunedModels] = useState([]);
  const [usePretrained, setUsePretrained] = useState(false);
  const [bf16, setBf16] = useState(false);
  const [saveSteps, setSaveSteps] = useState(200);
  const [gradientAccumulationSteps, setGradientAccumulationSteps] = useState(1);
  const [targetModules, setTargetModules] = useState("all-linear");

  useEffect(() => {
    fetchSavedModels();
    fetchUploadedDatasets();
    fetchFineTunedModels();
  }, []);

  const fetchSavedModels = () => {
    fetch(`${process.env.REACT_APP_SERVER_URL}/list_saved_models`)
      .then((response) => response.json())
      .then((data) => setSavedModels(data))
      .catch((error) => console.error("Error fetching saved models:", error));
  };

  const fetchUploadedDatasets = () => {
    fetch(`${process.env.REACT_APP_SERVER_URL}/list_uploaded_datasets`)
      .then((response) => response.json())
      .then((data) => setUploadedDatasets(data))
      .catch((error) =>
        console.error("Error fetching uploaded datasets:", error)
      );
  };

  const fetchFineTunedModels = () => {
    fetch(`${process.env.REACT_APP_SERVER_URL}/list_fine_tuned_models`)
      .then((response) => response.json())
      .then((data) => setFineTunedModels(data))
      .catch((error) =>
        console.error("Error fetching fine-tuned models:", error)
      );
  };

  const handleFineTuning = () => {
    if (!selectedModel.trim() || !selectedDataset.trim()) {
      alert(i18n("select_model_dataset"));
      return;
    }

    setIsFineTuning(true);
    setStatusMessage(i18n("fine_tuning_model"));

    const body = {
      model_file_name: selectedModel,
      dataset_file_name: selectedDataset,
      epochs,
      learning_rate: learningRate,
      batch_size: batchSize,
      use_pretrained: usePretrained,
      bf16,
      save_steps: saveSteps,
      gradient_accumulation_steps: gradientAccumulationSteps,
      target_modules: targetModules,
    };

    fetch(`${process.env.REACT_APP_SERVER_URL}/fine_tune_model`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(body),
    })
      .then((response) => response.json())
      .then((data) => {
        setStatusMessage(data.message);
        setIsFineTuning(false);
        fetchFineTunedModels();
      })
      .catch((error) => {
        console.error("Error fine-tuning model:", error);
        setStatusMessage(i18n("error_fine_tuning"));
        setIsFineTuning(false);
      });
  };

  return (
    <Box overflow="auto" pad="medium">
      <Box flex={{ shrink: 0 }}>
        <Heading level={2} textAlign="center">
          {i18n("model_fine_tuning")}
        </Heading>

        <Box margin={{ bottom: "medium" }}>
          <Select
            options={savedModels}
            value={selectedModel}
            placeholder={i18n("select_model_to_finetune")}
            onChange={({ option }) => setSelectedModel(option)}
            disabled={isFineTuning}
          />
        </Box>

        <Box margin={{ bottom: "medium" }}>
          <Select
            options={uploadedDatasets}
            value={selectedDataset}
            placeholder={i18n("select_dataset")}
            onChange={({ option }) => setSelectedDataset(option)}
            disabled={isFineTuning}
          />
        </Box>

        <Box margin={{ bottom: "medium" }} gap="medium">
          <Box direction="row" align="center" justify="between" gap="medium">
            <Text>{i18n("epochs")}</Text>
            <TextInput
              type="number"
              placeholder={i18n("eg_10")}
              value={epochs}
              onChange={(e) => setEpochs(Number(e.target.value))}
              disabled={isFineTuning}
              width="small"
            />
          </Box>
          <Box direction="row" align="center" justify="between">
            <Text>{i18n("learning_rate")}</Text>
            <TextInput
              type="number"
              step="0.0001"
              placeholder={i18n("eg_0_001")}
              value={learningRate}
              onChange={(e) => setLearningRate(Number(e.target.value))}
              disabled={isFineTuning}
              width="small"
            />
          </Box>
          <Box direction="row" align="center" justify="between" gap="medium">
            <Text>{i18n("batch_size")}</Text>
            <TextInput
              type="number"
              placeholder={i18n("eg_32")}
              value={batchSize}
              onChange={(e) => setBatchSize(Number(e.target.value))}
              disabled={isFineTuning}
              width="small"
            />
          </Box>
          <Box direction="row" align="center" justify="between" gap="medium">
            <Text>{i18n("save_steps")}</Text>
            <TextInput
              type="number"
              placeholder={i18n("eg_200")}
              value={saveSteps}
              onChange={(e) => setSaveSteps(Number(e.target.value))}
              disabled={isFineTuning}
              width="small"
            />
          </Box>
          <Box direction="row" align="center" justify="between" gap="medium">
            <Text>{i18n("gradient_accumulation_steps")}</Text>
            <TextInput
              type="number"
              placeholder={i18n("eg_1")}
              value={gradientAccumulationSteps}
              onChange={(e) =>
                setGradientAccumulationSteps(Number(e.target.value))
              }
              disabled={isFineTuning}
              width="small"
            />
          </Box>
          <Box direction="row" align="center" justify="between" gap="medium">
            <Text>{i18n("target_modules")}</Text>
            <TextInput
              placeholder={i18n("eg_all_linear")}
              value={targetModules}
              onChange={(e) => setTargetModules(e.target.value)}
              disabled={isFineTuning}
              width="small"
            />
          </Box>
          <Box direction="row" align="center" justify="between" gap="medium">
            <CheckBox
              checked={bf16}
              label={i18n("use_bf16")}
              onChange={(event) => setBf16(event.target.checked)}
              disabled={isFineTuning}
            />
          </Box>
          <Box direction="row" align="center" justify="between" gap="medium">
            <CheckBox
              checked={usePretrained}
              label={i18n("use_pretrained_weights")}
              onChange={(event) => setUsePretrained(event.target.checked)}
              disabled={isFineTuning}
            />
          </Box>
        </Box>

        <Box direction="row">
          <Button
            label={
              isFineTuning ? i18n("fine_tuning") : i18n("start_fine_tuning")
            }
            onClick={handleFineTuning}
            primary
            disabled={isFineTuning}
          />
        </Box>

        {statusMessage && (
          <Text margin={{ top: "medium" }} color="status-critical">
            {statusMessage}
          </Text>
        )}

        <Box>
          <Heading level={3} margin={{ top: "large", bottom: "small" }}>
            {i18n("fine_tuned_models")}
          </Heading>
          <List
            data={fineTunedModels}
            primaryKey={(model) => (
              <a
                href={`${process.env.REACT_APP_SERVER_URL}/download_fine_tuned_model/${model}`}
                download
              >
                {model}
              </a>
            )}
          />
        </Box>
      </Box>
    </Box>
  );
};

export default ModelFineTuning;
