import React, { useEffect, useState, useRef } from "react";
import { Box, TextInput, Button, Text, Heading, Layer } from "grommet";
import { useLocation, useNavigate } from "react-router-dom";
import { useTranslation } from "react-i18next";
import AppBreadcrumbs from "./AppBreadcrumbs"; // Import your custom AppBreadcrumbs
import { Edit, Trash } from "grommet-icons";

const Chats = () => {
  const [chatHistory, setChatHistory] = useState([]); // Initialize as empty
  const [inputValue, setInputValue] = useState("");
  const [selectedChatId, setSelectedChatId] = useState(null); // To store the session ID
  const [selectedModel, setSelectedModel] = useState(null);
  const [availableChats, setAvailableChats] = useState([]); // List of available chat histories
  const [renamingChatId, setRenamingChatId] = useState(null); // Store chat ID to rename
  const [newChatName, setNewChatName] = useState(""); // New chat name input
  const [chatToDelete, setChatToDelete] = useState(null); // Store the chat ID to delete
  const chatContainerRef = useRef(null);
  const [showDeleteConfirmation, setShowDeleteConfirmation] = useState(false);

  const location = useLocation();
  const navigate = useNavigate();
  const { t } = useTranslation(); // Initialize the translation hook

  // Fetch available chat histories
  useEffect(() => {
    fetch(`${process.env.REACT_APP_SERVER_URL}/list_chats`)
      .then((response) => response.json())
      .then((data) => {
        setAvailableChats(data);
      })
      .catch((err) => console.error("Error listing chat histories:", err));
  }, []);

  useEffect(() => {
    const queryParams = new URLSearchParams(location.search);
    const model = queryParams.get("model");
    setSelectedModel(model);
  }, [location.search, navigate]);

  useEffect(() => {
    fetchAvailableChats();
  }, []);

  useEffect(() => {
    if (chatContainerRef.current) {
      chatContainerRef.current.scrollTop =
        chatContainerRef.current.scrollHeight;
    }
  }, [chatHistory]);

  const fetchAvailableChats = () => {
    fetch(`${process.env.REACT_APP_SERVER_URL}/list_chats`)
      .then((response) => response.json())
      .then((data) => {
        setAvailableChats(data);
      })
      .catch((err) => console.error("Error listing chat histories:", err));
  };

  const handleSendMessage = () => {
    if (inputValue.trim() !== "" && selectedModel) {
      const newMessage = { role: "user", content: inputValue };
      const updatedChatHistory = [...chatHistory, newMessage];

      setChatHistory(updatedChatHistory);
      setInputValue("");

      if (!selectedChatId) {
        // Create new chat session if none exists
        fetch(`${process.env.REACT_APP_SERVER_URL}/chats`, {
          method: "POST",
        })
          .then((response) => response.json())
          .then((data) => {
            setSelectedChatId(data.id);
            navigate(`/chats?model=${selectedModel}&chat_id=${data.id}`);

            // Fetch the updated chat list
            fetchAvailableChats(); // <--- Refresh the chat list here

            return data.id;
          })
          .then((newChatId) => {
            fetch(`${process.env.REACT_APP_SERVER_URL}/chat`, {
              method: "POST",
              headers: {
                "Content-Type": "application/json",
              },
              body: JSON.stringify({
                chat_history: updatedChatHistory,
                user_input: inputValue,
              }),
            })
              .then((response) => response.json())
              .then((data) => {
                const botResponse = {
                  role: "assistant",
                  content: data.response,
                };
                setChatHistory((prevChatHistory) => [
                  ...prevChatHistory,
                  botResponse,
                ]);

                fetch(
                  `${process.env.REACT_APP_SERVER_URL}/chats/${newChatId}`,
                  {
                    method: "PUT",
                    headers: {
                      "Content-Type": "application/json",
                    },
                    body: JSON.stringify({
                      id: newChatId,
                      messages: [...updatedChatHistory, botResponse],
                      model: selectedModel,
                    }),
                  }
                ).catch((err) => console.error("Error saving chat:", err));
              })
              .catch((error) => {
                console.error("Error during chat:", error);
              });
          })
          .catch((err) => console.error("Error creating new chat:", err));
      } else {
        fetch(`${process.env.REACT_APP_SERVER_URL}/chat`, {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify({
            chat_history: updatedChatHistory,
            user_input: inputValue,
          }),
        })
          .then((response) => response.json())
          .then((data) => {
            const botResponse = { role: "assistant", content: data.response };
            setChatHistory((prevChatHistory) => [
              ...prevChatHistory,
              botResponse,
            ]);

            fetch(
              `${process.env.REACT_APP_SERVER_URL}/chats/${selectedChatId}`,
              {
                method: "PUT",
                headers: {
                  "Content-Type": "application/json",
                },
                body: JSON.stringify({
                  id: selectedChatId,
                  messages: [...updatedChatHistory, botResponse],
                  model: selectedModel,
                }),
              }
            ).catch((err) => console.error("Error saving chat:", err));
          })
          .catch((error) => {
            console.error("Error during chat:", error);
          });
      }
    }
  };

  const handleKeyPress = (event) => {
    if (event.key === "Enter") {
      handleSendMessage();
    }
  };
  const confirmDelete = (chatId) => {
    setChatToDelete(chatId); // Set the chat to delete
    setShowDeleteConfirmation(true); // Show the confirmation modal
  };
  const handleStartNewChat = () => {
    fetch(`${process.env.REACT_APP_SERVER_URL}/chats`, {
      method: "POST",
    })
      .then((response) => response.json())
      .then((data) => {
        setSelectedChatId(data.id);
        setChatHistory([]); // Clear chat history for the new session
        navigate(`/chats?model=${selectedModel}&chat_id=${data.id}`);

        // Fetch the updated chat list
        fetchAvailableChats(); // <--- Refresh the chat list after starting new chat
      })
      .catch((err) => console.error("Error creating new chat:", err));
  };

  // Handle selecting an existing chat history
  const handleSelectChat = (chatId) => {
    // Set the selected chat ID in state
    setSelectedChatId(chatId);

    // Fetch the chat history for the selected chat
    fetch(`${process.env.REACT_APP_SERVER_URL}/chats/${chatId}`)
      .then((response) => response.json())
      .then((data) => {
        // Update the chat history state with the fetched data
        setChatHistory(data.messages || []);
      })
      .catch((err) => console.error("Error loading chat history:", err));
  };

  // Handle renaming a chat
  const handleRenameChat = () => {
    if (newChatName.trim() === "") {
      console.error("Chat name cannot be empty");
      return;
    }

    fetch(
      `${process.env.REACT_APP_SERVER_URL}/chats/${renamingChatId}/rename/${newChatName}`,
      {
        method: "PUT",
        headers: {
          "Content-Type": "application/json",
        },
      }
    )
      .then((response) => {
        if (response.ok) {
          setAvailableChats((prevChats) =>
            prevChats.map((chat) =>
              chat.id === renamingChatId ? { ...chat, id: newChatName } : chat
            )
          );
          setRenamingChatId(null);
          setNewChatName("");

          fetchAvailableChats(); // <--- Refresh the chat list here
        } else {
          console.error("Error renaming chat");
        }
      })
      .catch((err) => console.error("Error renaming chat:", err));
  };

  // Handle deleting a chat
  const handleDeleteChat = () => {
    if (chatToDelete) {
      fetch(`${process.env.REACT_APP_SERVER_URL}/chats/${chatToDelete}`, {
        method: "DELETE",
      })
        .then((response) => {
          if (response.ok) {
            setAvailableChats((prevChats) =>
              prevChats.filter((chat) => chat.id !== chatToDelete)
            );
            if (selectedChatId === chatToDelete) {
              setChatHistory([]);
              setSelectedChatId(null);
              navigate(`/chats?model=${selectedModel}`);
            }
            setShowDeleteConfirmation(false);

            fetchAvailableChats(); // <--- Refresh the chat list here
          } else {
            console.error("Error deleting chat");
          }
        })
        .catch((err) => console.error("Error deleting chat:", err));
    }
  };

  // Define the breadcrumb hierarchy
  const breadcrumbHierarchy = [
    {
      label: t("modelSelectionChats"),
      link: "/modelSelectionChats", // Link to the model selection page
    },
    {
      label: t("chat"),
      link: "#", // Current page (chat)
    },
  ];

  return (
    <Box fill>
      <Box flex direction="row">
        <Box
          fill="vertical"
          width="20%"
          background="light-2"
          pad="medium"
          overflow="auto"
        >
          {/* Start New Chat Button */}
          <Button
            label={t("startNewChat")}
            onClick={handleStartNewChat}
            primary
            margin={{ bottom: "small" }}
          />

          {/* List of available chat histories */}
          <Box
            margin={{ vertical: "small" }}
            overflow={"auto"}
            flex={{ shrink: 0 }}
          >
            <Text>{t("availableChats")}:</Text>
            {availableChats.length > 0 ? (
              availableChats.map((chat) => (
                <Box
                  key={chat.id}
                  margin={{ vertical: "small" }}
                  direction="row"
                  overflow={"auto"}
                  flex={{ shrink: 0 }}
                >
                  <Button
                    label={`${chat.id.substring(0, 5)}...`} // Shortened chat name
                    onClick={() => handleSelectChat(chat.id)}
                    primary
                  />
                  <Button
                    icon={<Edit />}
                    onClick={() => {
                      setRenamingChatId(chat.id);
                      setNewChatName(chat.name);
                    }}
                    secondary
                  />
                  <Button
                    icon={<Trash />}
                    onClick={() => confirmDelete(chat.id)}
                    secondary
                  />
                </Box>
              ))
            ) : (
              <Text>{t("noChatsAvailable")}</Text>
            )}
          </Box>
        </Box>
        {/* Breadcrumb Navigation */}
        <Box flex background="light-1" pad="medium" gap="small">
          <AppBreadcrumbs hierarchy={breadcrumbHierarchy} />
          {/* Selected Model Heading */}
          <Heading level="3" margin="none">
            {`${t("selectedModel")}: ${selectedModel}`}
          </Heading>

          {/* Chat History */}
          <Box
            flex
            ref={chatContainerRef}
            pad={{ horizontal: "small", vertical: "small" }}
            border={{ color: "light-4", size: "xsmall" }}
            background="white"
            round="small"
            overflow="auto"
          >
            {chatHistory.length > 0 ? (
              chatHistory.map((message, index) => (
                <Box
                  key={index}
                  align={message.role === "user" ? "end" : "start"}
                  margin={{ vertical: "xsmall" }}
                  flex={{ shrink: 0 }}
                >
                  <Box
                    background={message.role === "user" ? "brand" : "light-3"}
                    pad={{ horizontal: "medium", vertical: "small" }}
                    round="small"
                  >
                    <Text>{message.content}</Text>
                  </Box>
                </Box>
              ))
            ) : (
              <Text>{t("noMessages")}</Text>
            )}
          </Box>

          {/* Input for sending messages */}
          <Box direction="row" gap="small">
            <TextInput
              value={inputValue}
              onChange={(event) => setInputValue(event.target.value)}
              onKeyPress={handleKeyPress}
              placeholder={t("typeMessage")}
            />
            <Button label={t("send")} onClick={handleSendMessage} primary />
          </Box>
        </Box>
      </Box>

      {/* Renaming Chat Modal */}
      {renamingChatId && (
        <Layer
          onEsc={() => setRenamingChatId(null)}
          onClickOutside={() => setRenamingChatId(null)}
        >
          <Box pad="medium" gap="small" width="medium">
            <Heading level="3">{t("renameChat")}</Heading>
            <TextInput
              value={newChatName}
              onChange={(event) => setNewChatName(event.target.value)}
              placeholder={t("newChatName")}
            />
            <Box direction="row" gap="small">
              <Button label={t("save")} onClick={handleRenameChat} primary />
              <Button
                label={t("cancel")}
                onClick={() => setRenamingChatId(null)}
              />
            </Box>
          </Box>
        </Layer>
      )}

      {showDeleteConfirmation && (
        <Layer
          onEsc={() => setShowDeleteConfirmation(false)}
          onClickOutside={() => setShowDeleteConfirmation(false)}
        >
          <Box pad="medium" gap="small" width="medium">
            <Text>{t("deleteConfirmation")}</Text> {/* Use translation key */}
            <Box direction="row" gap="small" justify="end">
              <Button
                label={t("cancel")} // Use translation key
                onClick={() => setShowDeleteConfirmation(false)}
              />
              <Button
                label={t("delete")} // Use translation key
                onClick={handleDeleteChat}
                primary
                color="status-critical"
              />
            </Box>
          </Box>
        </Layer>
      )}
    </Box>
  );
};

export default Chats;
