import React, { useState, useEffect } from "react";
import {
  Box,
  Table,
  TableHeader,
  TableRow,
  TableCell,
  TableBody,
  Heading,
  Text,
  Spinner,
  Pagination,
  Anchor,
} from "grommet";
import { LinkPrevious } from "grommet-icons";
import { useParams, useNavigate } from "react-router-dom";
import { useTranslation } from "react-i18next";

const DatasetDetails = () => {
  const { filename } = useParams();
  const navigate = useNavigate();
  const { t } = useTranslation(); // Initialize the translation hook
  const [datasetInfo, setDatasetInfo] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState("");
  const [currentPage, setCurrentPage] = useState(1);
  const [rowsPerPage] = useState(10); // Number of rows per page

  const fetchDatasetInfo = () => {
    fetch(
      `${process.env.REACT_APP_SERVER_URL}/dataset_info/${encodeURIComponent(
        filename
      )}?page=${currentPage}&rows_per_page=${rowsPerPage}`
    )
      .then((response) => {
        if (!response.ok) {
          throw new Error(t("datasetNotFound")); // Use translation key
        }
        return response.json();
      })
      .then((data) => setDatasetInfo(data))
      .catch((err) => {
        console.error(t("errorFetchingDatasetInfo"), err); // Use translation key
        setError(
          err.message || t("genericErrorFetchingDataset") // Use translation key
        );
      })
      .finally(() => setLoading(false));
  };

  useEffect(() => {
    fetchDatasetInfo();
  }, [filename, currentPage]);

  const handlePageChange = ({ page }) => {
    setCurrentPage(page);
  };

  if (loading) {
    return (
      <Box fill align="center" justify="center">
        <Spinner />
        <Text margin={{ top: "small" }}>{t("loadingDatasetDetails")}</Text>{" "}
        {/* Use translation key */}
      </Box>
    );
  }

  if (error) {
    return (
      <Box pad="medium" gap="medium" align="center">
        <Text color="status-critical">{error}</Text>
        <Anchor
          icon={<LinkPrevious />}
          label={t("backToDatasets")} // Use translation key
          onClick={() => navigate("/datasets")}
        />{" "}
        {/* Use translation key */}
      </Box>
    );
  }

  return (
    <Box pad="medium">
      <Anchor
        icon={<LinkPrevious />}
        label={t("backToDatasets")} // Use translation key
        onClick={() => navigate("/datasets")}
      />
      <Heading level={2} margin={{ top: "medium", bottom: "small" }}>
        {t("datasetInformation")} {/* Use translation key */}
      </Heading>
      <Text size="large" weight="bold" margin={{ bottom: "small" }}>
        {t("datasetName")}: {filename}
      </Text>
      <Box margin={{ bottom: "medium" }}>
        <Text>
          {t("rows")}: {datasetInfo.row_count}
        </Text>{" "}
        {/* Use translation key */}
        <Text>
          {t("columns")}:{" "}
          {datasetInfo.columns ? datasetInfo.columns.length : t("na")}
        </Text>{" "}
        {/* Use translation key */}
      </Box>
      <Text
        size="large"
        weight="bold"
        margin={{ bottom: "medium", vertical: "medium" }}
      >
        {t("datasetPreview")} {/* Use translation key */}
      </Text>
      <Box overflow="auto">
        <Table>
          <TableHeader>
            <TableRow>
              {datasetInfo.columns
                ? datasetInfo.columns.map((col) => (
                    <TableCell key={col} scope="col" border="bottom">
                      <Text weight="bold">{col}</Text>
                    </TableCell>
                  ))
                : Object.keys(datasetInfo.data_preview[0] || {}).map((key) => (
                    <TableCell key={key} scope="col" border="bottom">
                      <Text weight="bold">{key}</Text>
                    </TableCell>
                  ))}
            </TableRow>
          </TableHeader>
          <TableBody>
            {datasetInfo.data_preview.map((row, index) => (
              <TableRow key={index} border="bottom">
                {datasetInfo.columns
                  ? datasetInfo.columns.map((col) => (
                      <TableCell key={col} scope="row">
                        <Text>{row[col]}</Text>
                      </TableCell>
                    ))
                  : Object.keys(row).map((key) => (
                      <TableCell key={key} scope="row">
                        <Text>{row[key]}</Text>
                      </TableCell>
                    ))}
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </Box>
      <Pagination
        numberItems={datasetInfo.row_count}
        page={currentPage}
        step={rowsPerPage}
        onChange={handlePageChange}
        margin={{ top: "medium" }}
      />
    </Box>
  );
};

export default DatasetDetails;
